import React, { useEffect, useState } from 'react'
import styled from 'styled-components'
import { useTable, useFilters, useGlobalFilter, useAsyncDebounce, useSortBy, usePagination, useGroupBy, useExpanded } from 'react-table'
// A great library for fuzzy filtering/sorting items
import {matchSorter} from 'match-sorter'

import { CSVLink, CSVDownload } from "react-csv";

import { ScrollView } from 'react-native-gesture-handler'

const Styles = styled.div`
  padding: 1rem;

  table {
    border-spacing: 0;
    border: 1px solid #00ADF1;
    border-radius: 10px;
    font-family: Monaco;
    tr {
      :last-child {
        td {
          border-bottom: 0;
          font-family: Monaco;
        }
      }
    }
    th {
        background-color: #E6E6E6;
        border-spacing: 5;
        font-family: Monaco;
        padding: 0.5rem;
        border: 1px solid #FFFFFF;
        border-radius: 10px;
    },
    td {
      margin: 0;
      padding: 0.5rem;
      border-bottom: 1px solid #00ADF1;
      border-right: 1px solid #00ADF1;
      font-family: Monaco;
     

      :last-child {
        font-family: Monaco;
        border-right: 0;
      }
    }
  }
`


// Define a default UI for filtering
function GlobalFilter({
    preGlobalFilteredRows,
    globalFilter,
    setGlobalFilter,
}) {
    const count = preGlobalFilteredRows.length
    const [value, setValue] = React.useState(globalFilter)
    const onChange = useAsyncDebounce(value => {
        setGlobalFilter(value || undefined)
    }, 200)

    return (
        <span>
            Search:{' '}
            <input
                value={value || ""}
                onChange={e => {
                    setValue(e.target.value);
                    onChange(e.target.value);
                }}
                placeholder={`${count} records...`}
                style={{
                    fontSize: '1.1rem',
                    border: '0',
                }}
            />
        </span>
    )
}

// Define a default UI for filtering
function DefaultColumnFilter({
    column: { filterValue, preFilteredRows, setFilter },
}) {
    const count = preFilteredRows.length

    return (
        <input
            value={filterValue || ''}
            onChange={e => {
                setFilter(e.target.value || undefined) // Set undefined to remove the filter entirely
            }}
            placeholder={`Search ${count} records...`}
        />
    )
}

// This is a custom filter UI for selecting
// a unique option from a list
function SelectColumnFilter({
    column: { filterValue, setFilter, preFilteredRows, id },
}) {
    // Calculate the options for filtering
    // using the preFilteredRows
    const options = React.useMemo(() => {
        const options = new Set()
        preFilteredRows.forEach(row => {
            options.add(row.values[id])
        })
        return [...options.values()]
    }, [id, preFilteredRows])

    // Render a multi-select box
    return (
        <select
            value={filterValue}
            onChange={e => {
                setFilter(e.target.value || undefined)
            }}
        >
            <option value="">All</option>
            {options.map((option, i) => (
                <option key={i} value={option}>
                    {option}
                </option>
            ))}
        </select>
    )
}

// This is a custom filter UI that uses a
// slider to set the filter value between a column's
// min and max values
function SliderColumnFilter({
    column: { filterValue, setFilter, preFilteredRows, id },
}) {
    // Calculate the min and max
    // using the preFilteredRows

    const [min, max] = React.useMemo(() => {
        let min = preFilteredRows.length ? preFilteredRows[0].values[id] : 0
        let max = preFilteredRows.length ? preFilteredRows[0].values[id] : 0
        preFilteredRows.forEach(row => {
            min = Math.min(row.values[id], min)
            max = Math.max(row.values[id], max)
        })
        return [min, max]
    }, [id, preFilteredRows])

    return (
        <>
            <input
                type="range"
                min={min}
                max={max}
                value={filterValue || min}
                onChange={e => {
                    setFilter(parseInt(e.target.value, 10))
                }}
            />
            <button onClick={() => setFilter(undefined)}>Off</button>
        </>
    )
}

// This is a custom UI for our 'between' or number range
// filter. It uses two number boxes and filters rows to
// ones that have values between the two
function NumberRangeColumnFilter({
    column: { filterValue = [], preFilteredRows, setFilter, id },
}) {
    const [min, max] = React.useMemo(() => {
        let min = preFilteredRows.length ? preFilteredRows[0].values[id] : 0
        let max = preFilteredRows.length ? preFilteredRows[0].values[id] : 0
        preFilteredRows.forEach(row => {
            min = Math.min(row.values[id], min)
            max = Math.max(row.values[id], max)
        })
        return [min, max]
    }, [id, preFilteredRows])

    return (
        <div
            style={{
                display: 'flex',
            }}
        >
            <input
                value={filterValue[0] || ''}
                type="number"
                onChange={e => {
                    const val = e.target.value
                    setFilter((old = []) => [val ? parseInt(val, 10) : undefined, old[1]])
                }}
                placeholder={`Min (${min})`}
                style={{
                    width: '70px',
                    marginRight: '0.5rem',
                }}
            />
            to
            <input
                value={filterValue[1] || ''}
                type="number"
                onChange={e => {
                    const val = e.target.value
                    setFilter((old = []) => [old[0], val ? parseInt(val, 10) : undefined])
                }}
                placeholder={`Max (${max})`}
                style={{
                    width: '70px',
                    marginLeft: '0.5rem',
                }}
            />
        </div>
    )
}

function fuzzyTextFilterFn(rows, id, filterValue) {
    return matchSorter(rows, filterValue, { keys: [row => row.values[id]] })
}

// Let the table remove the filter if the string is empty
fuzzyTextFilterFn.autoRemove = val => !val
const SetOrderStatusName = (orderst, drivername) => {
    var i = "";
    if (orderst == "C") {
        i = "In Delivery";
        return i;
    } else if (orderst == "A") {
        i = "Not Assigned";
        return i;
    } else if (orderst == "D") {
        i = "Delivered";
        return i;
    } else if (orderst == "B") {
        i = "Assigned to: " + drivername;
        return i;
    } else if (orderst == "E") {
        i = "Refused";
        return i;
    }
};
// Our table component
function Table({ columns, data }) {
    const [csvData, setCsvData] = useState([]);
    for (var i = 0; i < data.length; i++) {
        var obj = data[i];


        for (var key in obj) {
            if (key == "order_status") {
                data[i][key] = SetOrderStatusName(obj[key], obj["driver"])

            }
            if (key == "payment_type") {
                if (data[i][key] == "A") {
                    data[i][key] = "Cash"
                }
                else {
                    data[i][key] = "Credit"
                }


            }
        }
    }

    const filterTypes = React.useMemo(
        () => ({
            // Add a new fuzzyTextFilterFn filter type.
            fuzzyText: fuzzyTextFilterFn,
            // Or, override the default text filter to use
            // "startWith"
            text: (rows, id, filterValue) => {
                return rows.filter(row => {
                    const rowValue = row.values[id]
                    return rowValue !== undefined
                        ? String(rowValue)
                            .toLowerCase()
                            .startsWith(String(filterValue).toLowerCase())
                        : true
                })
            },
        }),
        []
    )

    const defaultColumn = React.useMemo(
        () => ({
            // Let's set up our default Filter UI
            Filter: DefaultColumnFilter,
        }),
        []
    )

    const {
        getTableProps,
        getTableBodyProps,
        headerGroups,
        rows,
        page,
        prepareRow,
        // Instead of using 'rows', we'll use page,
        // which has only the rows for the active page
        // The rest of these things are super handy, too ;)
        canPreviousPage,
        canNextPage,
        pageOptions,
        pageCount,
        gotoPage,
        nextPage,
        previousPage,
        setPageSize,
        state: { pageIndex, pageSize },
        state,
        visibleColumns,
        preGlobalFilteredRows,
        setGlobalFilter

    } = useTable(
        {
            columns,
            data,
            defaultColumn, // Be sure to pass the defaultColumn option
            filterTypes,
            initialState: { pageIndex: 0 },
        },
        useFilters, // useFilters!
        useGlobalFilter, // useGlobalFilter!
        useGroupBy,
        useSortBy,
        useExpanded,
        usePagination,
    )




    useEffect(() => {

        var csvDataI = []

        for (var i = 0; i < rows.length; i++) {
            var row = []
            var obj = rows[i].values

            for (var key in obj) {
                if (key == "signature") {


                }
                else {

                    var value = obj[key];
                    if (Object.prototype.toString.call(value) === '[object Array]') {

                        var extraRow = []
                        for (var j = 0; j < value.length; j++) {

                            var obj2 = value[j]
                            for (var key2 in obj2) {
                                var nvalue = obj2[key2];

                                extraRow.push(nvalue)
                            }

                        }
                        row.push(extraRow.toString())
                    }
                    else {

                        row.push(value)



                    }
                }

            }

            csvDataI.push(row)
            setCsvData(csvDataI)

        }

    }, [rows])

    return (<ScrollView style={{ width: "100%", height: "100%" }}>
        <>
            <Legend />

            <table {...getTableProps()}>
                <thead>
                    {headerGroups.map(headerGroup => (
                        <tr {...headerGroup.getHeaderGroupProps()}>
                            {headerGroup.headers.map(column => (
                                <th {...column.getHeaderProps(column.getSortByToggleProps())}>
                                    {column.render('Header')}

                                    <span>
                                        {column.isSorted
                                            ? column.isSortedDesc
                                                ? ' '
                                                : ' '
                                            : ''}
                                    </span>



                                    {/* Render the columns filter UI */}
                                    <br />
                                    <br />
                                    <div>{column.canFilter ? column.render('Filter') : null}</div>
                                    <br />
                                    {column.canGroupBy ? (
                                        // If the column can be grouped, let's add a toggle
                                        <span {...column.getGroupByToggleProps()}>
                                            {column.isGrouped ? ' ' : ' '}
                                        </span>
                                    ) : null}
                                </th>
                            ))}
                        </tr>
                    ))}
                    <tr>
                        <th
                            colSpan={visibleColumns.length}
                            style={{
                                textAlign: 'left',
                            }}
                        >
                            <GlobalFilter
                                preGlobalFilteredRows={preGlobalFilteredRows}
                                globalFilter={state.globalFilter}
                                setGlobalFilter={setGlobalFilter}
                            />
                        </th>
                    </tr>
                </thead>
                <tbody {...getTableBodyProps()}>
                    {page.map((row, i) => {
                        prepareRow(row)
                        return (
                            <tr {...row.getRowProps()}>
                                {row.cells.map(cell => {
                                    return (
                                        <td
                                            // For educational purposes, let's color the
                                            // cell depending on what type it is given
                                            // from the useGroupBy hook
                                            {...cell.getCellProps()}
                                            style={{
                                                background: cell.isGrouped
                                                    ? '#0aff0082'
                                                    : cell.isAggregated
                                                        ? '#ffa50078'
                                                        : cell.isPlaceholder
                                                            ? '#ff000042'
                                                            : 'white',
                                            }}
                                        >
                                            {cell.isGrouped ? (
                                                // If it's a grouped cell, add an expander and row count
                                                <>
                                                    <span {...row.getToggleRowExpandedProps()}>
                                                        {row.isExpanded ? '' : ''}
                                                    </span>{' '}
                                                    {cell.render('Cell')} ({row.subRows.length})
                                                </>
                                            ) : cell.isAggregated ? (
                                                // If the cell is aggregated, use the Aggregated
                                                // renderer for cell
                                                cell.render('Aggregated')
                                            ) : cell.isPlaceholder ? null : ( // For cells with repeated values, render null
                                                // Otherwise, just render the regular cell
                                                cell.render('Cell')
                                            )}
                                        </td>
                                    )
                                })}
                            </tr>
                        )
                    })}
                </tbody>
            </table>
            <br />
            {/* 
Pagination can be built however you'd like. 
This is just a very basic UI implementation:
*/}
            <div className="pagination">
                <button onClick={() => gotoPage(0)} disabled={!canPreviousPage}>
                    {'<<'}
                </button>{' '}
                <button onClick={() => previousPage()} disabled={!canPreviousPage}>
                    {'<'}
                </button>{' '}
                <button onClick={() => nextPage()} disabled={!canNextPage}>
                    {'>'}
                </button>{' '}
                <button onClick={() => gotoPage(pageCount - 1)} disabled={!canNextPage}>
                    {'>>'}
                </button>{' '}
                <span>
                    Page{' '}
                    <strong>
                        {pageIndex + 1} of {pageOptions.length}
                    </strong>{' '}
                </span>
                <span>
                    | Go to page:{' '}
                    <input
                        type="number"
                        defaultValue={pageIndex + 1}
                        onChange={e => {
                            const page = e.target.value ? Number(e.target.value) - 1 : 0
                            gotoPage(page)
                        }}
                        style={{ width: '100px' }}
                    />
                </span>{' '}
                <select
                    value={pageSize}
                    onChange={e => {
                        setPageSize(Number(e.target.value))
                    }}
                >
                    {[10, 20, 30, 40, 50].map(pageSize => (
                        <option key={pageSize} value={pageSize}>
                            Show {pageSize}
                        </option>
                    ))}
                </select>
            </div>

            <CSVLink data={csvData} filename="caboria-dms-report.csv" target="_blank" className="btn btn-primary">Download as CSV</CSVLink>


        </>
    </ScrollView>

    )
}

// Define a custom filter filter function!
function filterGreaterThan(rows, id, filterValue) {
    return rows.filter(row => {
        const rowValue = row.values[id]
        return rowValue >= filterValue
    })
}



function Legend() {
    return (
        <div
            style={{
                padding: '0.5rem 0',
            }}
        >
            <span
                style={{
                    display: 'inline-block',
                    background: '#0aff0082',
                    padding: '0.5rem',
                }}
            >
                Grouped
            </span>{' '}
            <span
                style={{
                    display: 'inline-block',
                    background: '#ffa50078',
                    padding: '0.5rem',
                }}
            >
                Aggregated
            </span>{' '}
            <span
                style={{
                    display: 'inline-block',
                    background: '#ff000042',
                    padding: '0.5rem',
                }}
            >
                Repeated Value
            </span>
        </div>
    )
}

// This is an autoRemove method on the filter function that
// when given the new filter value and returns true, the filter
// will be automatically removed. Normally this is just an undefined
// check, but here, we want to remove the filter if it's not a number
filterGreaterThan.autoRemove = val => typeof val !== 'number'

function SortedFilteredDriverReport({ orders }) {


    const columns = React.useMemo(
        () => [

            {
                Header: 'Date',
                accessor: 'date',

                aggregate: 'count',
                Aggregated: ({ value }) => `${value} Dates`,
            },
            {
                Header: 'Assigned orders',
                accessor: 'number_of_orders_assigned',
                aggregate: 'count',
                Aggregated: ({ value }) => `${value} Orders`,

            },
            {
                Header: 'Picked up orders',
                accessor: 'number_of_orders_pickedup',
                aggregate: 'count',
                Aggregated: ({ value }) => `${value} Orders`,

            },
            {
                Header: 'Delivered orders',
                accessor: 'number_of_orders_delivered',
                aggregate: 'count',
                Aggregated: ({ value }) => `${value} Orders`,

            },
            {
                Header: 'Refused orders',
                accessor: 'number_of_orders_refused',
                aggregate: 'count',
                Aggregated: ({ value }) => `${value} Orders`,

            },
            {
                Header: 'Checked in',
                accessor: 'check_in',

                aggregate: 'count',
                Aggregated: ({ value }) => `${value} Check-ins`,
            },
            {
                Header: 'Checked out',
                accessor: 'check_out',
                aggregate: 'count',
                Aggregated: ({ value }) => `${value} Check-outs`,

            },


        ],
        []
    )



    return (
        <Styles>
            <Table columns={columns} data={orders} />
        </Styles>
    )
}



export default SortedFilteredDriverReport;
