import React, { useState } from "react";
import {
  SafeAreaView,
  StyleSheet,
  Text,
  Image,
  View,
  ImageBackground,
  TextInput,
  Touchable,
  TouchableOpacity,
} from "react-native";
import Config from "../../config";
import { Ionicons, FontAwesome } from "@expo/vector-icons";
import logout from "./Logout";
import Dashboard from "./Dashboard";
import { createDrawerNavigator } from "@react-navigation/drawer";
import Drivers from "../drivers/Drivers";

import Orders from "../orders/orders";
import Companies from "../companies/companies";
import ProfilePage from "../profile/profile";
import Admins from "../Admins/Admins";

import Statistics from "../Statistics/Statistics";
import Report from "../Reports/reports-orders";
import ReportDriver from "../Reports/reports-drivers";



const Drawer = createDrawerNavigator();
function MenuSuperAdmin({ navigation }) {
  return (
    <Drawer.Navigator
      initialRouteName="Dashboard"
      screenOptions={{
        headerShown: false,
        swipeEnabled: false,
        drawerStyle: {
          backgroundColor: Config.color_1,
          width: 240,
        },
        drawerPosition: "left",
      }}
    >
      <Drawer.Screen
        options={{
          title: "Dashboard",
          orientation: "all",
          drawerInactiveBackgroundColor: "#E6E6E6",
          drawerActiveBackgroundColor: "#E6E6E6",
          drawerLabelStyle: {
            color: "black",
          },
          drawerIcon: ({ focused, size }) => (
            <Ionicons
              name="map-sharp"
              size={size}
              color={focused ? "black" : "black"}
            />
          ),
        }}
        name="Dashboard"
        component={Dashboard}
      />

      <Drawer.Screen
        options={{
          title: "Profile",
          orientation: "all",
          drawerInactiveBackgroundColor: "#E6E6E6",
          drawerActiveBackgroundColor: "#E6E6E6",
          drawerLabelStyle: {
            color: "black",
          },
          drawerIcon: ({ focused, size }) => (
            <Ionicons
              name="person"
              size={size}
              color={focused ? "black" : "black"}
            />
          ),
        }}
        name="ProfilePage"
        component={ProfilePage}
      />


      <Drawer.Screen
        options={{
          title: "Admins",
          orientation: "all",
          drawerInactiveBackgroundColor: "#E6E6E6",
          drawerActiveBackgroundColor: "#E6E6E6",
          drawerLabelStyle: {
            color: "black",
          },
          drawerIcon: ({ focused, size }) => (
            <Ionicons
              name="people"
              size={size}
              color={focused ? "black" : "black"}
            />
          ),
        }}
        name="Admins"
        component={Admins}
      />
      {/* 
    <Drawer.Screen
        options={{
          title: "Clients",
          orientation: "all",
          drawerInactiveBackgroundColor: "#E6E6E6",
          drawerActiveBackgroundColor: "#E6E6E6",
          drawerLabelStyle: {
            color: "black",
          },
          drawerIcon: ({ focused, size }) => (
            <Ionicons
              name="people"
              size={size}
              color={focused ? "black" : "black"}
            />
          ),
        }}
        name="Clients"
        component={clients}
      /> */}



      <Drawer.Screen
        options={{
          title: "Orders",
          orientation: "all",
          drawerInactiveBackgroundColor: "#E6E6E6",
          drawerActiveBackgroundColor: "#E6E6E6",
          drawerLabelStyle: {
            color: "black",
          },
          drawerIcon: ({ focused, size }) => (
            <Ionicons
              name="restaurant"
              size={size}
              color={focused ? "black" : "black"}
            />
          ),
        }}
        name="Restaurant"
        component={Orders}
      />
      <Drawer.Screen
        options={{
          title: "Orders Reports",
          orientation: "all",
          drawerInactiveBackgroundColor: "#E6E6E6",
          drawerActiveBackgroundColor: "#E6E6E6",
          drawerLabelStyle: {
            color: "black",
          },
          drawerIcon: ({ focused, size }) => (
            <Ionicons
              name="md-newspaper-outline"
              size={size}
              color={focused ? "black" : "black"}
            />
          ),
        }}
        name="Reports"
        component={Report}
      />

      <Drawer.Screen
        options={{
          title: "Statistics",
          orientation: "all",
          drawerInactiveBackgroundColor: "#E6E6E6",
          drawerActiveBackgroundColor: "#E6E6E6",
          drawerLabelStyle: {
            color: "black",
          },
          drawerIcon: ({ focused, size }) => (
            <Ionicons
              name="podium"
              size={size}
              color={focused ? "black" : "black"}
            />
          ),
        }}
        name="Statistics"
        component={Statistics}
      />

      <Drawer.Screen
        options={{
          title: "Drivers",
          orientation: "all",
          drawerInactiveBackgroundColor: "#E6E6E6",
          drawerActiveBackgroundColor: "#E6E6E6",
          drawerLabelStyle: {
            color: "black",
          },
          drawerIcon: ({ focused, size }) => (
            <FontAwesome
              name="drivers-license"
              size={size}
              color={focused ? "black" : "black"}
            />
          ),
        }}
        name="Drivers"
        component={Drivers}
      />
      <Drawer.Screen
        options={{
          title: "Drivers Reports",
          orientation: "all",
          drawerInactiveBackgroundColor: "#E6E6E6",
          drawerActiveBackgroundColor: "#E6E6E6",
          drawerLabelStyle: {
            color: "black",
          },
          drawerIcon: ({ focused, size }) => (
            <Ionicons
              name="md-newspaper-outline"
              size={size}
              color={focused ? "black" : "black"}
            />
          ),
        }}
        name="Driver Reports"
        component={ReportDriver}
      />
      <Drawer.Screen
        options={{
          title: "Companies",
          orientation: "all",
          drawerInactiveBackgroundColor: "#E6E6E6",
          drawerActiveBackgroundColor: "#E6E6E6",
          drawerLabelStyle: {
            color: "black",
          },
          drawerIcon: ({ focused, size }) => (
            <FontAwesome
              name="building"
              size={size}
              color={focused ? "black" : "black"}
            />
          ),
        }}
        name="Companies"
        component={Companies}
      />
      <Drawer.Screen
        options={{
          title: "Logout",
          orientation: "all",
          drawerInactiveBackgroundColor: Config.color_1,
          drawerActiveBackgroundColor: Config.color_1,
          drawerLabelStyle: {
            color: "black",
          },
          drawerIcon: ({ focused, size }) => (
            <Ionicons
              name="log-out"
              size={size}
              color={focused ? "black" : "black"}
            />
          ),
        }}
        name="Logout"
        component={logout}
      />
    </Drawer.Navigator>
  );
}

export default MenuSuperAdmin;
