import React, { useEffect, useState } from "react";
import {
  Image,
  StyleSheet,
  Text,
  TextInput,
  TouchableOpacity,
  View,
  ScrollView,
} from "react-native";
import {
  Ionicons,
  MaterialCommunityIcons,
  Foundation,
  FontAwesome,
  Entypo,
} from "@expo/vector-icons";
import Config from "../../config";
import Loader from "react-loader-spinner";
import axios from "axios";
import AwesomeAlert from "react-native-awesome-alerts";
import Sidebar from "../dashboard/Sidebar";

const Companies = ({ navigation }) => {
  const [companiesSearch, onChangeCompaniesSearch] = useState("");
  const [showSideMenu, setShowSideMenu] = useState(false);
  const [editCompany, setEditCompany] = useState(null);
  const [isNewCompany, setIsNewCompany] = useState(false);
  const [loading, setLoading] = useState(true);
  const [companies, setCompanies] = useState([]);
  const [allCompanies, setAllCompanies] = useState([]);
  const [companyName, onChangeCompanyName] = useState("");
  const [description, onChangeDescription] = useState("");
  const [website, onChangeWebsite] = useState("");
  const [showAlert, setShowAlert] = useState(false);
  const [mobilephone, onChangeMobilePhone] = useState("");
  const [email, onChangeEmail] = useState("");
  const [password, onChangePassword] = useState("");
  const [password2, onChangePassword2] = useState("");
  const [showError, setShowError] = useState(false);
  const [errorHeader, setErrorHeader] = useState("");
  const [errorBody, setErrorBody] = useState("");

  const updateCompany = () => {
    setLoading(true);
    setShowSideMenu(false);

    axios
      .patch(
        Config.host +
          Config.port +
          "/api/companies/updatecompany/?company_id=" +
          editCompany.company_number,
        {
          company_name:
            companyName == "" ? editCompany.company_name : companyName,
          company_description:
            description == "" ? editCompany.company_description : description,
          company_website:
            website == "" ? editCompany.company_website : website,
        }
      )
      .then((response) => {
        axios

          .get(Config.host + Config.port + "/api/companies/getallcompanies/")
          .then((response) => {
            console.log(response.data);
            setCompanies(response.data);
            setAllCompanies(response.data);
            setLoading(false);
            ShowErrorFunction("", "Company edited successfully.");
          })
          .catch((e) => {
            setLoading(false);
            console.log(e);
          });
      })
      .catch((e) => {
        setLoading(false);
        ShowErrorFunction("Alert", "Failed to Edit company please try again.");
        console.log(e);
      });
    onChangeCompanyName("");
    onChangeDescription("");
    onChangeWebsite("");
    onChangeEmail("");
    onChangeMobilePhone("");
    onChangePassword2("");
    onChangePassword2("");
  };

  const ShowErrorFunction = (header, body) => {
    setErrorHeader(header);
    setErrorBody(body);
    setShowError(true);
  };

  const addCompany = () => {
    setLoading(true);
    setShowSideMenu(false);
    setIsNewCompany(false);
    console.log("CREAT Company");
    console.log({
      name: companyName,
      password: password,
      password2: password2,
      email: email,
      mphone: mobilephone,
      is_company: true,
      account_type: "Co",
      role: "N/A",
      branch: "-1",
    });
    axios
      .post(Config.host + Config.port + "/api/account/register", {
        name: companyName,
        password: password,
        password2: password2,
        email: email,
        mphone: mobilephone,
        is_company: true,
        account_type: "Co",
        role: "N/A",
        branch: "-1",
      })
      .then((response) => {
        var token = response.data.token;
        axios
          .post(
            Config.host +
              Config.port +
              "/api/companies/createcompany/?token=" +
              token,
            {
              company_name: companyName,
              company_description: description,
              company_website: website,
            }
          )
          .then((response) => {
            axios
              .get(
                Config.host + Config.port + "/api/companies/getallcompanies/"
              )
              .then((response) => {
                console.log(response.data);
                setCompanies(response.data);
                setAllCompanies(response.data);
                setLoading(false);
                ShowErrorFunction("", "Company created successfully.");
              })
              .catch((e) => {
                setLoading(false);
                ShowErrorFunction(
                  "Alert",
                  "Failed to create company please try again."
                );
                console.log(e);
              });
          })
          .catch((e) => {
            setLoading(false);
            ShowErrorFunction(
              "Alert",
              "Failed to create company please try again"
            );
            console.log(e);
          });
      })
      .catch((e) => {
        setLoading(false);
        ShowErrorFunction(
          "Alert",
          "Failed to create company please try again."
        );
        console.log(e);
      });
    onChangeCompanyName("");
    onChangeDescription("");
    onChangeWebsite("");
    onChangeEmail("");
    onChangeMobilePhone("");
    onChangePassword2("");
    onChangePassword2("");
  };

  const deleteCompany = (company) => {
    setLoading(true);
    setShowSideMenu(false);
    console.log(editCompany);
    axios
      .delete(
        Config.host +
          Config.port +
          "/api/companies/deletecompany/?company_id=" +
          company.company_number
      )
      .then((response) => {
        axios
          .get(Config.host + Config.port + "/api/companies/getallcompanies/")
          .then((response) => {
            setCompanies(response.data);
            setAllCompanies(response.data);
            setLoading(false);
            ShowErrorFunction("", "Company deleted successfully.");
          })
          .catch((e) => {
            setLoading(false);
            console.log(e);
            ShowErrorFunction(
              "Alert",
              "Failed to delete this company please try again."
            );
          });
      });
  };

  useEffect(() => {
    setLoading(true);
    axios
      .get(Config.host + Config.port + "/api/companies/getallcompanies/")
      .then((response) => {
        console.log(response.data);
        setCompanies(response.data);
        setAllCompanies(response.data);
        setLoading(false);
      })
      .catch((e) => {
        setLoading(false);
        console.log(e);
      });
  }, []);

  useEffect(() => {
    setLoading(true);
    var matchedCompanies = [];
    for (var i in allCompanies) {
      if (allCompanies[i].company_name.includes(companiesSearch)) {
        matchedCompanies.push(allCompanies[i]);
      } else if (
        allCompanies[i].company_description.includes(companiesSearch)
      ) {
        matchedCompanies.push(allCompanies[i]);
      } else if (allCompanies[i].company_website.includes(companiesSearch)) {
        matchedCompanies.push(allCompanies[i]);
      }
    }
    if (matchedCompanies.length != 0) {
      setCompanies(matchedCompanies);
      setLoading(false);
    } else if (companiesSearch == "") {
      setCompanies(allCompanies);
    } else {
      setCompanies([]);
      setLoading(false);
    }
  }, [companiesSearch]);

  return (
    <View style={{ flex: 1, backgroundColor: Config.color_1 }}>
      <Sidebar navigation={navigation} />

      <View
        style={{
          height: "100%",
          width: "95%",
          justifyContent: "flex-end",
          alignSelf: "flex-end",
          flexDirection: "row",
        }}
      >
        <View
          style={{
            height: "100%",
            width: "100%",
            alignSelf: "flex-end",
          }}
        >
          <View
            style={{
              backgroundColor: "white",
              alignItems: "center",
              flex: 1,
            }}
          >
            <View style={styles.gap}></View>
            <View
              style={{
                flexDirection: "row",
                alignSelf: "flex-start",
                paddingLeft: "5%",
              }}
            >
              <Image
                style={{ width: 90, height: 80 }}
                source={require("../../assets/logo-01.png")}
              />
              <View style={styles.gap}></View>
              <Text
                style={{
                  paddingLeft: 10,
                  color: Config.color_1,
                  alignSelf: "center",
                  fontWeight: "bold",
                  fontSize: 20,
                }}
              >
                Caboria Delivery Management System
              </Text>
            </View>

            <View
              style={{
                flexDirection: "row",
                alignSelf: "flex-end",
                paddingRight: "5%",
              }}
            >
              <View
                style={{
                  backgroundColor: Config.color_2,
                  width: 800,
                  height: 40,
                  borderRadius: 20,
                  alignContent: "center",
                }}
              >
                <View
                  style={{
                    width: "100%",
                    height: "100%",
                    flexDirection: "row",
                    justifyContent: "center",
                    alignItems: "center",
                    alignContent: "center",
                  }}
                >
                  <Ionicons size={24} name="search" onPress={() => {}} />
                  <View style={{ width: 20 }}></View>
                  <TextInput
                    style={{
                      borderColor: "black",
                      backgroundColor: "white",
                      borderRadius: 20,
                      width: "90%",
                      height: "80%",
                      paddingLeft: 20,
                    }}
                    autoCorrect={false}
                    placeholder={"Search Companies"}
                    onChangeText={onChangeCompaniesSearch}
                  ></TextInput>
                </View>
              </View>
              <View style={{ width: 40 }} />
              <TouchableOpacity
                style={{
                  backgroundColor: Config.color_2,
                  alignSelf: "flex-end",
                  width: 200,
                  height: 40,
                  borderRadius: 20,
                  justifyContent: "center",
                  flexDirection: "row",
                  alignItems: "center",
                  alignSelf: "center",
                }}
                onPress={() => {
                  setIsNewCompany(true);
                  setShowSideMenu(true);
                }}
              >
                <Ionicons size={24} name="add" />
                <View style={{ width: 20 }} />
                <Text style={{ fontWeight: "bold" }}>Add Company</Text>
              </TouchableOpacity>
            </View>
            <View style={styles.gap}></View>
            <View style={styles.gap}></View>
            <View style={styles.gap}></View>
            <View style={styles.gap}></View>
            {loading ? (
              <View
                style={{
                  width: "90%",
                  height: "75%",
                  justifyContent: "center",
                  alignContent: "center",
                  alignSelf: "center",
                  alignItems: "center",
                }}
              >
                <Loader color={Config.color_1} />
              </View>
            ) : (
              <ScrollView
                style={{
                  backgroundColor: Config.color_1,
                  width: "90%",
                  height: "75%",
                  borderRadius: 20,
                }}
              >
                {companies.map((company) => (
                  <View key={company.company_number}>
                    <View style={{ height: 10 }} />
                    <View
                      style={{
                        width: "90%",
                        borderRadius: 20,
                        padding: 20,
                        flexDirection: "row",
                        justifyContent: "space-evenly",
                        alignContent: "center",
                        alignSelf: "center",
                        backgroundColor: "#E6E6E6",
                      }}
                    >
                      <View
                        style={{
                          justifyContent: "center",
                          alignItems: "center",
                          alignContent: "center",
                          alignSelf: "center",
                        }}
                      >
                        <FontAwesome
                          name="building"
                          size={30}
                          color={Config.color_2}
                        />
                        <View style={{ height: 5 }}></View>
                        <Text style={{ fontSize: 12, fontWeight: "bold" }}>
                          Company Name
                        </Text>
                        <View style={{ height: 5 }}></View>
                        <View
                          style={{
                            alignItems: "center",
                            justifyContent: "center",
                            width: 200,
                          }}
                        >
                          <Text
                            style={{
                              fontSize: 12,

                              alignContent: "center",
                              alignItems: "center",
                              alignSelf: "center",
                              justifyContent: "center",
                            }}
                          >
                            {company.company_name}
                          </Text>
                        </View>
                      </View>
                      <View style={{ width: 10 }}></View>

                      <View
                        style={{
                          justifyContent: "center",
                          alignItems: "center",
                          alignContent: "center",
                          alignSelf: "center",
                        }}
                      >
                        <FontAwesome
                          name="phone"
                          size={30}
                          color={Config.color_2}
                        />
                        <View style={{ height: 5 }}></View>
                        <Text style={{ fontSize: 12, fontWeight: "bold" }}>
                          Company Number
                        </Text>
                        <View style={{ height: 5 }}></View>
                        <View
                          style={{
                            alignItems: "center",
                            justifyContent: "center",
                            width: 200,
                          }}
                        >
                          <Text
                            style={{
                              fontSize: 12,

                              alignContent: "center",
                              alignItems: "center",
                              alignSelf: "center",
                              justifyContent: "center",
                            }}
                          >
                            {company.company_number}
                          </Text>
                        </View>
                      </View>

                      <View style={{ width: 10 }}></View>
                      <View
                        style={{
                          justifyContent: "center",
                          alignItems: "center",
                          alignContent: "center",
                          alignSelf: "center",
                        }}
                      >
                        <FontAwesome
                          name="file-text"
                          size={30}
                          color={Config.color_2}
                        />
                        <View style={{ height: 5 }}></View>
                        <Text style={{ fontSize: 12, fontWeight: "bold" }}>
                          Company Description
                        </Text>
                        <View style={{ height: 5 }}></View>
                        <View
                          style={{
                            alignItems: "center",
                            justifyContent: "center",
                            width: 200,
                          }}
                        >
                          <Text
                            style={{
                              fontSize: 12,

                              alignContent: "center",
                              alignItems: "center",
                              alignSelf: "center",
                              justifyContent: "center",
                            }}
                          >
                            {company.company_description}
                          </Text>
                        </View>
                      </View>
                      <View style={{ width: 10 }}></View>
                      <View style={{ width: 10 }}></View>
                      <View
                        style={{
                          justifyContent: "center",
                          alignItems: "center",
                          alignContent: "center",
                          alignSelf: "center",
                        }}
                      >
                        <Foundation
                          name="web"
                          size={30}
                          color={Config.color_2}
                        />
                        <View style={{ height: 5 }}></View>
                        <Text style={{ fontSize: 12, fontWeight: "bold" }}>
                          Company Website
                        </Text>
                        <View style={{ height: 5 }}></View>
                        <View
                          style={{
                            alignItems: "center",
                            justifyContent: "center",
                            width: 200,
                          }}
                        >
                          <Text
                            style={{
                              fontSize: 12,

                              alignContent: "center",
                              alignItems: "center",
                              alignSelf: "center",
                              justifyContent: "center",
                            }}
                          >
                            {company.company_website}
                          </Text>
                        </View>
                      </View>
                      <View style={{ width: 10 }}></View>
                      <View style={{ width: 10 }}></View>

                      <View
                        style={{
                          justifyContent: "center",
                          alignItems: "center",
                        }}
                      >
                        <View style={{ height: 5 }}></View>
                        <Text style={{ fontSize: 12, fontWeight: "bold" }}>
                          Edit
                        </Text>
                        <View style={{ height: 5 }}></View>
                        <Ionicons
                          name="pencil"
                          size={24}
                          onPress={() => {
                            setShowSideMenu(true);
                            setEditCompany(company);
                          }}
                        />
                      </View>

                      <View
                        style={{
                          justifyContent: "center",
                          alignItems: "center",
                        }}
                      >
                        {showAlert &&
                        company.company_name == editCompany.company_name ? (
                          <View
                            style={{
                              backgroundColor: Config.color_2,
                              position: "absolute",
                              top: 5,
                              right: 0,
                              width: 200,
                              height: 100,
                              borderRadius: 20,
                              alignItems: "center",
                              justifyContent: "center",
                              zIndex: 888,
                            }}
                          >
                            <Text>Delete Company?</Text>
                            <Text style={{ fontWeight: "bold" }}>
                              Are you sure?
                            </Text>
                            <View style={{ height: 5 }}></View>
                            <View style={{ flexDirection: "row" }}>
                              <Ionicons
                                name="checkmark"
                                size={24}
                                color="green"
                                onPress={() => {
                                  setShowAlert(false);
                                  deleteCompany(company);
                                }}
                              />
                              <View style={{ width: 5 }}></View>
                              <View style={{ width: 5 }}></View>
                              <View style={{ width: 5 }}></View>
                              <Entypo
                                name="cross"
                                size={24}
                                color="red"
                                onPress={() => {
                                  setShowAlert(false);
                                }}
                              />
                            </View>
                          </View>
                        ) : (
                          <View></View>
                        )}
                        <View style={{ height: 5 }}></View>
                        <Text style={{ fontSize: 12, fontWeight: "bold" }}>
                          Delete
                        </Text>
                        <View style={{ height: 5 }}></View>
                        <Ionicons
                          name="trash-bin"
                          color="#E60000"
                          size={24}
                          onPress={() => {
                            setEditCompany(company);
                            setShowAlert(true);
                          }}
                        />
                      </View>
                    </View>
                  </View>
                ))}
              </ScrollView>
            )}
            {showSideMenu ? (
              <View
                style={{
                  flex: 1,
                  position: "absolute",
                  right: "0%",
                  top: "0%",
                  height: "100%",
                  width: "30%",
                  backgroundColor: "#00000090",
                  justifyContent: "flex-start",
                  alignItems: "center",
                  zIndex: 999,
                }}
              >
                <View
                  style={{
                    padding: 5,
                    position: "absolute",
                    left: "0%",
                    top: "0%",
                  }}
                >
                  <Ionicons
                    size={25}
                    name="ios-close-circle"
                    onPress={() => {
                      setShowSideMenu(false);
                      setIsNewCompany(false);
                    }}
                  />
                </View>

                {isNewCompany ? (
                  <View
                    style={{
                      flex: 1,
                      justifyContent: "flex-start",
                      alignItems: "center",
                      width: "90%",
                    }}
                  >
                    <ScrollView style={{ width: "100%" }}>
                      <View style={{ height: 10 }}></View>
                      <View style={{ height: 10 }}></View>
                      <Image
                        style={{ width: 90, height: 80, alignSelf: "center" }}
                        source={require("../../assets/logo-01.png")}
                      />
                      <View style={{ height: 10 }}></View>
                      <View style={{ height: 10 }}></View>
                      <View style={{ height: 10 }}></View>
                      <View style={{ height: 10 }}></View>
                      <View style={{ height: 10 }}></View>
                      <View style={{ height: 10 }}></View>
                      <View
                        style={{
                          justifyContent: "center",
                          alignItems: "center",
                          width: "100%",
                        }}
                      >
                        <FontAwesome
                          name="building"
                          size={20}
                          color={Config.color_2}
                        />
                        <View style={{ height: 5 }}></View>
                        <Text style={{ color: "white", fontSize: 14 }}>
                          Company's Name
                        </Text>
                        <View style={{ height: 5 }}></View>
                        <View style={{ height: 5 }}></View>
                        <TextInput
                          style={{
                            borderColor: "white",
                            backgroundColor: "white",
                            borderRadius: 20,
                            width: "80%",
                            height: 40,
                            paddingLeft: 15,
                          }}
                          autoCorrect={false}
                          placeholder={"Company's Name"}
                          placeholderTextColor="black"
                          onChangeText={onChangeCompanyName}
                          backgroundColor="white"
                        ></TextInput>
                      </View>

                      <View style={{ height: 10 }}></View>
                      <View style={{ height: 10 }}></View>

                      <View
                        style={{
                          justifyContent: "center",
                          alignItems: "center",
                          width: "100%",
                        }}
                      >
                        <Ionicons
                          name="call"
                          size={20}
                          color={Config.color_2}
                        />
                        <View style={{ height: 5 }}></View>
                        <Text style={{ color: "white", fontSize: 14 }}>
                          Mobile Number
                        </Text>
                        <View style={{ height: 5 }}></View>

                        <View style={{ height: 5 }}></View>
                        <TextInput
                          style={{
                            borderColor: "white",
                            backgroundColor: "white",
                            borderRadius: 20,
                            width: "80%",
                            height: 40,
                            paddingLeft: 15,
                          }}
                          autoCompleteType={"tel"}
                          autoCorrect={false}
                          placeholder={"Mobile Phone"}
                          placeholderTextColor="black"
                          backgroundColor="white"
                          onChangeText={onChangeMobilePhone}
                        ></TextInput>
                      </View>
                      <View style={{ height: 10 }}></View>
                      <View style={{ height: 10 }}></View>
                      <View
                        style={{
                          justifyContent: "center",
                          alignItems: "center",
                          width: "100%",
                        }}
                      >
                        <Ionicons
                          name="mail"
                          size={20}
                          color={Config.color_2}
                        />
                        <View style={{ height: 5 }}></View>
                        <Text style={{ color: "white", fontSize: 14 }}>
                          Email
                        </Text>
                        <View style={{ height: 5 }}></View>

                        <View style={{ height: 5 }}></View>
                        <TextInput
                          style={{
                            borderColor: "white",
                            backgroundColor: "white",
                            borderRadius: 20,
                            width: "80%",
                            height: 40,
                            paddingLeft: 15,
                          }}
                          autoCorrect={false}
                          placeholder={"Email"}
                          placeholderTextColor="black"
                          backgroundColor="white"
                          onChangeText={onChangeEmail}
                        ></TextInput>
                      </View>

                      <View style={{ height: 10 }}></View>
                      <View style={{ height: 10 }}></View>

                      <View
                        style={{
                          justifyContent: "center",
                          alignItems: "center",
                          width: "100%",
                        }}
                      >
                        <FontAwesome
                          name="file-text"
                          size={20}
                          color={Config.color_2}
                        />
                        <View style={{ height: 5 }}></View>
                        <Text style={{ color: "white", fontSize: 14 }}>
                          Company's Description
                        </Text>
                        <View style={{ height: 5 }}></View>

                        <View style={{ height: 5 }}></View>
                        <TextInput
                          style={{
                            borderColor: "white",
                            backgroundColor: "white",
                            borderRadius: 20,
                            width: "80%",
                            height: 40,
                            paddingLeft: 15,
                          }}
                          autoCompleteType={"tel"}
                          autoCorrect={false}
                          placeholder={"Company's Description"}
                          placeholderTextColor="black"
                          backgroundColor="white"
                          onChangeText={onChangeDescription}
                        ></TextInput>
                      </View>
                      <View style={{ height: 10 }}></View>
                      <View style={{ height: 10 }}></View>
                      <View
                        style={{
                          justifyContent: "center",
                          alignItems: "center",
                          width: "100%",
                        }}
                      >
                        <Foundation
                          name="web"
                          size={20}
                          color={Config.color_2}
                        />
                        <View style={{ height: 5 }}></View>
                        <Text style={{ color: "white", fontSize: 14 }}>
                          Company's Website
                        </Text>
                        <View style={{ height: 5 }}></View>

                        <View style={{ height: 5 }}></View>
                        <TextInput
                          style={{
                            borderColor: "white",
                            backgroundColor: "white",
                            borderRadius: 20,
                            width: "80%",
                            height: 40,
                            paddingLeft: 15,
                          }}
                          autoCorrect={false}
                          placeholder={"Company's Website"}
                          placeholderTextColor="black"
                          backgroundColor="white"
                          onChangeText={onChangeWebsite}
                        ></TextInput>
                      </View>
                      <View style={{ height: 10 }}></View>
                      <View style={{ height: 10 }}></View>
                      <View
                        style={{
                          justifyContent: "center",
                          alignItems: "center",
                          width: "100%",
                        }}
                      >
                        <Ionicons
                          name="lock-closed"
                          size={20}
                          color={Config.color_2}
                        />
                        <View style={{ height: 5 }}></View>
                        <Text style={{ color: "white", fontSize: 14 }}>
                          Password
                        </Text>
                        <View style={{ height: 5 }}></View>

                        <View style={{ height: 5 }}></View>
                        <TextInput
                          style={{
                            borderColor: "white",
                            backgroundColor: "white",
                            borderRadius: 20,
                            width: "80%",
                            height: 40,
                            paddingLeft: 15,
                          }}
                          autoCompleteType={"tel"}
                          autoCorrect={false}
                          placeholder={"Password"}
                          textContentType={"password"}
                          secureTextEntry={true}
                          placeholderTextColor="black"
                          backgroundColor="white"
                          onChangeText={onChangePassword}
                        ></TextInput>
                      </View>
                      <View style={{ height: 10 }}></View>
                      <View style={{ height: 10 }}></View>
                      <View
                        style={{
                          justifyContent: "center",
                          alignItems: "center",
                          width: "100%",
                        }}
                      >
                        <Ionicons
                          name="lock-closed"
                          size={20}
                          color={Config.color_2}
                        />
                        <View style={{ height: 5 }}></View>
                        <Text style={{ color: "white", fontSize: 14 }}>
                          Confirm Password
                        </Text>
                        <View style={{ height: 5 }}></View>

                        <View style={{ height: 5 }}></View>
                        <TextInput
                          style={{
                            borderColor: "white",
                            backgroundColor: "white",
                            borderRadius: 20,
                            width: "80%",
                            height: 40,
                            paddingLeft: 15,
                          }}
                          secureTextEntry={true}
                          textContentType={"password"}
                          autoCorrect={false}
                          placeholder={"Confirm Password"}
                          placeholderTextColor="black"
                          backgroundColor="white"
                          onChangeText={onChangePassword2}
                        ></TextInput>
                      </View>

                      <View style={{ height: 10 }}></View>
                      <View style={{ height: 10 }}></View>
                      <View style={{ height: 10 }}></View>
                      <View style={{ height: 10 }}></View>
                      <TouchableOpacity
                        style={{
                          backgroundColor: Config.color_2,

                          width: 200,
                          height: 40,
                          borderRadius: 20,
                          justifyContent: "center",
                          flexDirection: "row",
                          alignItems: "center",
                          alignSelf: "center",
                        }}
                        onPress={addCompany}
                      >
                        <Ionicons size={24} name="add" />
                        <View style={{ width: 20 }} />
                        <Text style={{ fontWeight: "bold" }}>Add Company</Text>
                      </TouchableOpacity>
                    </ScrollView>
                  </View>
                ) : (
                  <View
                    style={{
                      flex: 1,
                      justifyContent: "flex-start",
                      alignItems: "center",
                      width: "90%",
                    }}
                  >
                    <ScrollView style={{ width: "100%" }}>
                      <View style={{ height: 10 }}></View>
                      <View style={{ height: 10 }}></View>
                      <View style={{ alignSelf: "center" }}>
                        <Image
                          style={{ width: 90, height: 80 }}
                          source={require("../../assets/logo-01.png")}
                        />
                      </View>
                      <View style={{ height: 10 }}></View>
                      <View style={{ height: 10 }}></View>
                      <View style={{ height: 10 }}></View>
                      <View style={{ height: 10 }}></View>
                      <View style={{ height: 10 }}></View>
                      <View style={{ height: 10 }}></View>
                      <View
                        style={{
                          justifyContent: "center",
                          alignItems: "center",
                          width: "100%",
                        }}
                      >
                        <FontAwesome
                          name="building"
                          size={20}
                          color={Config.color_2}
                        />
                        <View style={{ height: 5 }}></View>
                        <Text style={{ color: "white", fontSize: 14 }}>
                          Company's Name
                        </Text>
                        <View style={{ height: 5 }}></View>
                        <View style={{ height: 5 }}></View>
                        <TextInput
                          style={{
                            borderColor: "white",
                            backgroundColor: "white",
                            borderRadius: 20,
                            width: "80%",
                            height: 40,
                            paddingLeft: 15,
                          }}
                          autoCorrect={false}
                          placeholder={editCompany.company_name}
                          // value={editCompany.company_name}
                          placeholderTextColor="black"
                          onChangeText={onChangeCompanyName}
                          backgroundColor="white"
                        ></TextInput>
                      </View>
                      <View style={{ height: 10 }}></View>
                      <View style={{ height: 10 }}></View>

                      <View
                        style={{
                          justifyContent: "center",
                          alignItems: "center",
                          width: "100%",
                        }}
                      >
                        <FontAwesome
                          name="file-text"
                          size={20}
                          color={Config.color_2}
                        />
                        <View style={{ height: 5 }}></View>
                        <Text style={{ color: "white", fontSize: 14 }}>
                          Company's Description
                        </Text>
                        <View style={{ height: 5 }}></View>

                        <View style={{ height: 5 }}></View>
                        <TextInput
                          style={{
                            borderColor: "white",
                            backgroundColor: "white",
                            borderRadius: 20,
                            width: "80%",
                            height: 40,
                            paddingLeft: 15,
                          }}
                          autoCompleteType={"tel"}
                          autoCorrect={false}
                          // value={editCompany.company_description}
                          placeholder={editCompany.company_description}
                          placeholderTextColor="black"
                          backgroundColor="white"
                          onChangeText={onChangeDescription}
                        ></TextInput>
                      </View>
                      <View style={{ height: 10 }}></View>
                      <View style={{ height: 10 }}></View>
                      <View
                        style={{
                          justifyContent: "center",
                          alignItems: "center",
                          width: "100%",
                        }}
                      >
                        <Foundation
                          name="web"
                          size={20}
                          color={Config.color_2}
                        />
                        <View style={{ height: 5 }}></View>
                        <Text style={{ color: "white", fontSize: 14 }}>
                          Company's Website
                        </Text>
                        <View style={{ height: 5 }}></View>

                        <View style={{ height: 5 }}></View>
                        <TextInput
                          style={{
                            borderColor: "white",
                            backgroundColor: "white",
                            borderRadius: 20,
                            width: "80%",
                            height: 40,
                            paddingLeft: 15,
                          }}
                          autoCorrect={false}
                          // value={editCompany.company_website}
                          placeholder={editCompany.company_website}
                          placeholderTextColor="black"
                          backgroundColor="white"
                          onChangeText={onChangeWebsite}
                        ></TextInput>
                      </View>

                      <View style={{ height: 10 }}></View>
                      <View style={{ height: 10 }}></View>
                      <View style={{ height: 10 }}></View>
                      <View style={{ height: 10 }}></View>
                      <TouchableOpacity
                        style={{
                          backgroundColor: Config.color_2,
                          alignSelf: "center",
                          width: 200,
                          height: 40,
                          borderRadius: 20,
                          justifyContent: "center",
                          flexDirection: "row",
                          alignItems: "center",
                        }}
                        onPress={updateCompany}
                      >
                        <Ionicons size={24} name="save" />
                        <View style={{ width: 20 }} />
                        <Text style={{ fontWeight: "bold" }}>Save</Text>
                      </TouchableOpacity>
                    </ScrollView>
                  </View>
                )}
              </View>
            ) : (
              <View />
            )}
            {showError ? (
              <View
                style={{
                  backgroundColor: Config.color_2,
                  position: "absolute",
                  alignSelf: "center",
                  marginTop: "20%",
                  width: 400,
                  height: 200,
                  borderRadius: 20,
                  // alignItems: "center",
                  // justifyContent: "center",
                  zIndex: 888,
                }}
              >
                <View
                  style={{
                    flexDirection: "row",
                    width: "100%",
                    justifyContent: "space-between",
                  }}
                >
                  <Image
                    style={{
                      width: 34,
                      height: 30,
                      marginTop: "5%",
                      marginLeft: "5%",
                    }}
                    source={require("../../assets/logo-01.png")}
                  />

                  <View
                    style={{
                      alignSelf: "flex-end",
                      marginTop: "5%",
                      marginRight: "5%",
                    }}
                  >
                    <Entypo
                      name="cross"
                      size={24}
                      color="black"
                      onPress={() => {
                        setShowError(false);
                      }}
                    />
                  </View>
                </View>

                <Text
                  style={{
                    alignSelf: "center",
                    fontWeight: "bold",
                    color: "red",
                    fontSize: 24,
                  }}
                >
                  {errorHeader}
                </Text>
                <View style={{ margin: "5%" }}>
                  <Text
                    style={{
                      alignSelf: "center",
                      fontWeight: "bold",
                      fontSize: 16,
                    }}
                  >
                    {errorBody}
                  </Text>
                </View>
                <View style={{ height: 5 }}></View>
              </View>
            ) : (
              <View></View>
            )}
          </View>
        </View>
      </View>
    </View>
  );
};

var styles = StyleSheet.create({
  container: {
    alignItems: "center",
    justifyContent: "center",
  },
  image: {
    flex: 1,
  },
  input: {
    backgroundColor: Config.color_1,
    borderRadius: 20,
    width: "33%",
    height: "25%",
    left: 51,
    top: "50%",
    alignItems: "center",
    justifyContent: "flex-start",
  },
  gap: {
    height: 10,
    width: 5,
  },
  field: {
    justifyContent: "center",
    borderRadius: 20,
    width: "90%",
    height: "25%",
    padding: 20,
    backgroundColor: "white",
  },
  button: {
    justifyContent: "center",
    alignItems: "center",
    borderRadius: 20,
    width: "90%",
    height: "25%",
    padding: 20,
    backgroundColor: Config.color_2,
  },
});

export default Companies;
