import React, { useEffect, useState } from "react";
import {
    Image,
    SafeAreaView,
    ScrollView,
    StyleSheet,
    Text,
    TextInput,
    TouchableOpacity,
    View,
    Picker
} from "react-native";
import Config from "../../config";
import {
    Ionicons,
    FontAwesome,
    MaterialCommunityIcons,
} from "@expo/vector-icons";
import {
    withScriptjs,
    withGoogleMap,
    GoogleMap,
    Marker,
} from "react-google-maps";
import axios from "axios";
import MarkerWithLabel from "react-google-maps/lib/components/addons/MarkerWithLabel";
import { StatusBar } from "expo-status-bar";
import Loader from "react-loader-spinner";
import moment, { now } from "moment";
import Sidebar from "../dashboard/Sidebar";

import SortedFilteredTable from "./sortedFilteredTable"








const Report = ({ navigation }) => {
    const [selectedValueBranch, setSelectedValueBranch] = useState("-1");
    const [branches, setBranches] = useState([[]]);
    const [orders, setOrders] = useState([]);
    const [admin, setAdmin] = useState([]);
    const [loading, setLoading] = useState([]);

    useEffect(() => {
        setLoading(true)
        Config.account_type = "SA"
        axios
            .get(Config.host + Config.port + "/api/orders/getallbranches/")
            .then((response) => {

                setBranches(response.data);
                axios
                    .get(
                        Config.host +
                        Config.port +
                        "/api/account/getaccountbytoken/?token=" +
                        Config.token
                    )
                    .then((adminnResponse) => {
                        setAdmin(adminnResponse.data);
                        axios
                            .get(
                                Config.host +
                                Config.port +
                                "/api/account/getbranchbytoken/?token=" +
                                Config.token
                            )
                            .then((response) => {
                                console.log(response.data);
                                setSelectedValueBranch(response.data);
                                setLoading(false)

                            })
                            .catch((e) => {
                                console.log(e);
                                setLoading(false)

                            });
                    });
            }
            )
            .catch((e) => {
                console.log(e)
                setLoading(false)
            })

    }, [])

    useEffect(() => {
        setLoading(true)
        setOrders([])
        axios
            .get(
                Config.host +
                Config.port +
                "/api/orders/getallorders/?branch=" +
                selectedValueBranch.toString()
            )
            .then((response) => {
                console.log(response.data)
                setOrders(response.data)
                setLoading(false)
            })
            .catch((e) => {
                console.log(e)
                setLoading(false)
            })

    }, [selectedValueBranch])

    useEffect(() => {


    }, [orders])
    return (
        <View style={{ flex: 1, backgroundColor: "#FFFF" }}>

            <View style={styles.gap}></View>
            <View
                style={{
                    flexDirection: "row",
                    alignSelf: "flex-start",
                    paddingLeft: "7%",
                }}
            >
                <Image
                    style={{ width: 90, height: 80 }}
                    source={require("../../assets/logo-01.png")}
                />
                <View style={styles.gap}></View>
                <Text
                    style={{
                        paddingLeft: 10,
                        color: Config.color_1,
                        fontWeight: "bold",
                        alignSelf: "center",
                        fontSize: 24,
                    }}
                >
                    Orders Reports Page
                </Text>
            </View>
            <View style={{ width: 20, height: "5%" }} />



            <Sidebar navigation={navigation} />

            <View
                style={{
                    flexDirection: "row",
                    alignSelf: "flex-end",
                    paddingRight: "5%",
                }}
            >
                <View style={{ width: 40 }} />

                <View style={styles.gap}></View>
                <View style={styles.gap}></View>
            </View>
            <View style={styles.container2}>
                <View style={styles.gap}></View>
                <View style={styles.gap}></View>




                {admin.account_type == "SA" ? (
                    <View
                        style={{
                            justifyContent: "center",
                            alignItems: "center",
                            flexDirection: "row",
                            width: "100%",
                        }}
                    >
                        <View
                            style={{
                                flexDirection: "row",
                                alignItems: "center",
                                width: "80%",
                                marginHorizontal: "5%",
                            }}
                        >
                            <Text
                                style={{
                                    color: Config.color_1,
                                    fontSize: 20,
                                    fontWeight: "bold",
                                    marginLeft: "8%",
                                    marginRight: "3%",
                                    alignContent: "center",
                                    alignItems: "center",
                                    alignSelf: "center",
                                }}
                            >
                                <Ionicons
                                    size={20}
                                    name="business-sharp"
                                    color={Config.color_1}
                                />
                                {"  "}
                                Branch
                            </Text>

                            <Picker
                                style={{
                                    borderColor: Config.color_1,
                                    backgroundColor: Config.color_1,
                                    borderRadius: 20,
                                    width: "50%",
                                    height: 40,
                                    paddingLeft: 15,
                                }}
                                selectedValue={selectedValueBranch}
                                onValueChange={(itemValue, itemIndex) =>
                                    setSelectedValueBranch(itemValue)
                                }
                            >
                                <Picker.Item label="Select branch" value={-1} />
                                {branches.map((branches) => (
                                    <Picker.Item key={branches} label={branches[1]} value={branches[0]} />
                                ))}
                            </Picker>
                        </View>

                    </View>
                ) : (
                    <View></View>
                )}


                <View style={styles.gap}></View>
                <View style={styles.gap}></View>

                {
                    loading ? (
                        <View
                            style={{
                                width: "90%",
                                height: "75%",
                                justifyContent: "center",
                                alignContent: "center",
                                alignSelf: "center",
                                alignItems: "center",
                            }}
                        >
                            <Loader color={Config.color_1} />
                        </View>
                    ) : (

                        orders.length != 0 ? <ScrollView>

                            <SortedFilteredTable orders={orders} />
                        </ScrollView> : <View
                            style={{
                                width: "90%",
                                height: "75%",
                                justifyContent: "center",
                                alignContent: "center",
                                alignSelf: "center",
                                alignItems: "center",
                            }}
                        >

                            {selectedValueBranch == '-1' ?
                                <Text>Please select a branch</Text>
                                :
                                <Text>No orders in this branch yet</Text>
                            }
                        </View>

                    )
                }





            </View>
        </View>
    )

}



var styles = StyleSheet.create({
    containertable: {
        flex: 1,
        padding: 16,
        paddingTop: 30,
        backgroundColor: "#fff",
    },
    headtable: { height: 40, backgroundColor: "#f1f8ff" },
    texttable: { margin: 6 },

    scrollcontainer: {
        flexDirection: "row",
        backgroundColor: "white",
        margin: "1%",
        borderRadius: 20,
    },

    scrollcontainer2: {
        flexDirection: "row",
        marginLeft: "6%",
        alignContent: "center",
        alignItems: "center",
        justifyContent: "space-evenly",
    },

    graph: {
        flexDirection: "row",
        alignSelf: "center",
        alignContent: "center",
        alignItems: "center",
        justifyContent: "center",
    },

    ScrolIcon11: {
        width: "45%",
    },

    ScrolIcon12: {
        width: "5%",
    },

    ScrolIcon13: {
        width: "50%",
    },

    ScrolIcon1: {
        borderRadius: 20,
        display: "flex",
        backgroundColor: "#A3E5FF",
        flexDirection: "row",
        justifyContent: "space-evenly",
        margin: "1%",
        alignItems: "center",
        width: "40%",
        height: 100,
    },

    ScrolIcon2: {
        borderRadius: 20,
        borderColor: "#A3E5FF",
        borderWidth: 2,
        display: "flex",
        backgroundColor: "#A3E5FF",
        flexDirection: "row",
        justifyContent: "space-evenly",
        margin: "1%",
        alignItems: "center",
        width: "25%",
        height: 100,
    },

    ScrolIcon22: {
        borderRadius: 20,
        borderColor: "#A3E5FF",
        borderWidth: 2,
        display: "flex",
        backgroundColor: "#A3E5FF",
        flexDirection: "row",
        justifyContent: "space-evenly",
        margin: "1%",
        alignItems: "center",
        width: "13%",
        height: 50,
    },

    datebutton: {
        borderRadius: 20,
        borderColor: Config.color_1,
        borderWidth: 2,
        display: "flex",
        backgroundColor: Config.color_1,
        flexDirection: "row",
        justifyContent: "space-evenly",
        margin: "1%",
        marginTop: "6%",
        alignItems: "center",
        width: 210,
        height: 30,
    },

    ScrolIcon3: {
        borderRadius: 20,
        borderColor: "#A3E5FF",
        borderWidth: 2,
        display: "flex",
        backgroundColor: "#A3E5FF",
        flexDirection: "row",
        justifyContent: "space-evenly",
        margin: "1%",
        alignItems: "center",
        width: "13%",
        height: 100,
    },

    ScrolIcon4: {
        borderRadius: 20,
        borderWidth: 2,
        display: "flex",
        flexDirection: "row",
        justifyContent: "space-evenly",
        margin: "1%",
        alignItems: "center",
        width: "13%",
        height: 100,
    },

    ScrolIcon5: {
        display: "flex",
        flexDirection: "column",
        justifyContent: "space-evenly",
        margin: "1%",
        alignItems: "center",
        width: "5%",
        height: 100,
    },

    container2: {
        flex: 1,
        marginLeft: "6%",
        marginRight: "1%",
        borderRadius: 15,
    },

    scrollView: {
        backgroundColor: "#E6E6E6",
        marginHorizontal: 20,
        borderRadius: 15,
    },

    text: {
        fontSize: 42,
    },

    titleText: {
        fontSize: 25,
        fontWeight: "bold",
        marginLeft: "1%",
        color: Config.color_1,
    },

    menu: {
        position: "absolute",
        flex: 1,
        alignSelf: "flex-start",
        alignItems: "center",
        justifyContent: "flex-start",
        width: "5%",
        flexDirection: "column",
        backgroundColor: Config.color_1,
        height: "100%",
    },

    container: {
        height: 100,
        width: "100%",
        alignItems: "center",
    },

    Image: {
        height: 80,
        width: 90,
        alignItems: "center",
        marginTop: "1%",
    },

    input: {
        backgroundColor: Config.color_1,
        borderRadius: 20,
        width: "33%",
        height: "25%",
        left: 51,
        top: "50%",
        alignItems: "center",
        justifyContent: "flex-start",
    },

    gap: {
        height: 10,
    },

    field: {
        justifyContent: "center",
        borderRadius: 20,
        width: "90%",
        height: "25%",
        padding: 20,
        backgroundColor: "white",
    },

    button: {
        justifyContent: "center",
        alignItems: "center",
        borderRadius: 20,
        width: "90%",
        height: "25%",
        padding: 20,
        backgroundColor: Config.color_2,
    },
});



export default Report;
