import React, { useEffect, useState } from "react";
import {
  Image,
  SafeAreaView,
  ScrollView,
  StyleSheet,
  Text,
  View,
} from "react-native";
import Config from "../../config";
import { Ionicons, MaterialCommunityIcons } from "@expo/vector-icons";
import {
  withScriptjs,
  withGoogleMap,
  GoogleMap,
  Marker,
} from "react-google-maps";

import MarkerWithLabel from "react-google-maps/lib/components/addons/MarkerWithLabel";
import { StatusBar } from "expo-status-bar";
import moment, { now } from "moment";
import { BorderlessButton } from "react-native-gesture-handler";
import axios from "axios";

const orderDelivered = ({ route, navigation }) => {
  const [clientName, setOrderClientName] = React.useState("");
  const [clientLocation, setOrderStatusclientLocation] = React.useState("");
  const [clientPhone, setOrderStatusClientPhone] = React.useState("");
  const [orderNumber, setOrderNumber] = React.useState("");
  const [OrderInfo, setOrderInfo] = React.useState("");
  const [OrderDate, setOrderDate] = React.useState("");
  const [driverName, setOrderdriverName] = React.useState("");
  const [driverPhone, setOrderStatusdriverPhone] = React.useState("");
  const [driverCompany, setOrderStatusdriverCompany] = React.useState("");
  const [OrderPickupTime, setOrderPickupTime] = React.useState("");
  const [OrderDeliveredTime, setOrderDeliveredTime] = React.useState("");
  const [duration, setDuration] = React.useState({
    numhours: 0,
    nummins: 0,
  });

  const getDriver = (driverid) => {
    axios
      .get(
        Config.host +
          Config.port +
          "/api/profiles/getdriverbymphone/?driver_id=" +
          driverid
      )
      .then((response) => {
        console.log(response);
        setOrderdriverName(response.data.driver_name);
        setOrderStatusdriverPhone(response.data.mobilephone);
        setOrderStatusdriverCompany(response.data.company);
      })
      .catch((e) => {
        console.log(e);
      });
  };

  const orderitems = (items) => {
    var strin = " ";
    for (var i in items) {
      strin =
        strin + items[i].item_quantity + "x " + items[i].item_name_real + "+";
    }
    return strin;
  };

  useEffect(() => {
    setOrderNumber(route.params.order_id);
    setOrderDate(
      moment(route.params.fullorder.order_date_time).format(
        "YYYY/MM/DD hh:mm:ss a"
      )
    );
    setOrderStatusclientLocation(route.params.fullorder.customer_location);
    setOrderPickupTime(
      moment(route.params.fullorder.order_pickup_time).format(
        "YYYY/MM/DD hh:mm:ss a"
      )
    );
    setOrderDeliveredTime(
      moment(route.params.fullorder.order_delivery_time).format(
        "YYYY/MM/DD hh:mm:ss a"
      )
    );
    setOrderInfo(orderitems(route.params.fullorder.items));
    setOrderClientName(route.params.fullorder.client_name);
    setOrderStatusClientPhone(route.params.fullorder.client_mphone);
    setOrderdriverName(route.params.fullorder.driver_name);

    var m1 = moment(route.params.fullorder.order_date_time);
    var m2 = moment(
      route.params.fullorder.order_delivery_time);
    var m3 = m2.diff(m1, "minutes");
    var m4 = m2.diff(m1, "h");
    var days = Math.floor(m3 / 1440);
    var hours = Math.floor((m3 % 1440) / 60);
    var mins = Math.floor((m3 % 1440) % 60);
    setDuration({ numhours: hours, nummins: mins });
    console.log( moment.duration(m2.diff(m1)));
   
    // // TODO: Get Driver Info
    getDriver(route.params.fullorder.driver);
  }, []);

  return (
    <View style={{ flex: 1, backgroundColor: "#FFFF" }}>
      <View style={{ height: 50 }} />

      <View style={styles.container}>
        <Image
          style={styles.Image}
          source={require("../../assets/logo-01.png")}
        />
      </View>

      <View style={{ position: "absolute", left: "2%", top: "5%" }}>
        <Ionicons
          size={40}
          name="arrow-back"
          onPress={() => {
            navigation.navigate("Restaurant");
          }}
        />
      </View>

      <Text style={styles.titleText}>
        Order #{orderNumber}
        {"\n"}
      </Text>

      <SafeAreaView style={styles.container2}>
        <View style={styles.scrollcontainerOrderDetails}>
          <View
            style={[
              styles.ScrolIcon3,
              { backgroundColor: Config.color_2 },
              { width: "50%" },
            ]}
          >
            <ScrollView style={{ width: "100%", height: "100%" }}>
              <Text
                style={{
                  fontSize: 15,
                  width: "60%",
                  alignSelf: "center",
                  direction: "rtl",
                }}
              >
                <Text
                  style={{
                    fontSize: 15,
                    width: "60%",
                    alignSelf: "center",
                    fontWeight: "bold",
                    marginTop: "2%"
                  }}
                >
                  Order Details
                </Text>

                {"\n"}
                <Text
                  style={{
                    fontSize: 15,
                    width: "60%",
                    alignSelf: "center",
                    direction: "rtl",
                  }}
                >
                  {OrderInfo}
                </Text>
              </Text>
            </ScrollView>

            <Ionicons name="restaurant"  style={{ margin: "2%" }} size={20} color="black" />
          </View>
        </View>

        <View style={styles.scrollcontainer}>
          <View style={styles.ScrolIcon1}>
            <View style={styles.ScrolIcon11}>
              <Text style={{ fontSize: 15, width: "60%", alignSelf: "center" }}>
                <Text
                  style={{
                    fontSize: 15,
                    width: "60%",
                    alignSelf: "center",
                    fontWeight: "bold",
                  }}
                >
                  Client
                </Text>
                {"\n"}
                <Ionicons
                  name="person"
                  size={20}
                  color="black"
                  alignSelf="center"
                />
                {clientName}
              </Text>
            </View>
            <View style={styles.ScrolIcon12}>
              <Ionicons
                name="call"
                size={20}
                color="black"
                alignSelf="center"
              />
              <Ionicons
                name="location-sharp"
                size={20}
                color="black"
                alignSelf="center"
              />
            </View>

            <View style={styles.ScrolIcon11}>
              <Text
                style={{ fontSize: 15, width: "100%", alignSelf: "center" }}
              >
                {clientPhone}
                {"\n"}

                {clientLocation}
              </Text>
            </View>
          </View>

          <View style={styles.ScrolIcon3}>
            <Text style={{ fontSize: 15, width: "60%", alignSelf: "center" }}>
              <Text
                style={{
                  fontSize: 15,
                  width: "60%",
                  alignSelf: "center",
                  fontWeight: "bold",
                }}
              >
                Order Time
              </Text>
              {"\n"}
              {OrderDate}
            </Text>
            <Ionicons name="time" size={20} color="black" />
          </View>

          <View style={styles.ScrolIcon3}>
            <Text style={{ fontSize: 15, width: "60%", alignSelf: "center" }}>
              <Text
                style={{
                  fontSize: 15,
                  width: "60%",
                  alignSelf: "center",
                  fontWeight: "bold",
                }}
              >
                Order Delivered Time
              </Text>
              {"\n"}
              {OrderDeliveredTime}
            </Text>
            <Ionicons name="time" size={20} color="black" />
          </View>
        </View>

        <View style={styles.scrollcontainer}>
          <View style={styles.ScrolIcon1}>
            <View style={styles.ScrolIcon11}>
              <Text style={{ fontSize: 15, width: "60%", alignSelf: "center" }}>
                <Text
                  style={{
                    fontSize: 15,
                    width: "60%",
                    alignSelf: "center",
                    fontWeight: "bold",
                  }}
                >
                  Driver
                </Text>
                {"\n"}
                <Ionicons
                  name="person"
                  size={20}
                  color="black"
                  alignSelf="center"
                />
                {driverName}
              </Text>
            </View>
            <View style={styles.ScrolIcon12}>
              <Ionicons
                name="call"
                size={20}
                color="black"
                alignSelf="center"
              />
              <Ionicons
                name="business-sharp"
                size={20}
                color="black"
                alignSelf="center"
              />
            </View>
            <View style={styles.ScrolIcon13}>
              <Text
                style={{ fontSize: 15, width: "100%", alignSelf: "center" }}
              >
                {driverPhone}
                {"\n"}
                {driverCompany}
              </Text>
            </View>
          </View>

          <View style={styles.ScrolIcon3}>
            <Text style={{ fontSize: 15, width: "60%", alignSelf: "center" }}>
              <Text
                style={{
                  fontSize: 15,
                  width: "60%",
                  alignSelf: "center",
                  fontWeight: "bold",
                }}
              >
                Order Pickup Time
              </Text>

              {"\n"}
              {OrderPickupTime}
            </Text>
            <Ionicons name="time" size={20} color="black" />
          </View>

          <View style={styles.ScrolIcon3}>
            <Text style={{ fontSize: 15, width: "60%", alignSelf: "center" }}>
              <Text
                style={{
                  fontSize: 15,
                  width: "60%",
                  alignSelf: "center",
                  fontWeight: "bold",
                }}
              >
                Order Delivery Duration
              </Text>
              {"\n"}
              {duration.numhours} Hours and {duration.nummins} Minutes
            </Text>
            <Ionicons name="time" size={20} color="black" />
          </View>
        </View>
      </SafeAreaView>
    </View>
  );
};

var styles = StyleSheet.create({
  scrollcontainer: {
    flexDirection: "row",
  },

  scrollcontainerOrderDetails: {
    flexDirection: "row",
    justifyContent: "center",
  },
  ScrolIcon11: {
    width: "45%",
  },

  ScrolIcon12: {
    width: "5%",
  },

  ScrolIcon13: {
    width: "50%",
  },

  ScrolIcon1: {
    borderRadius: 20,
    display: "flex",
    backgroundColor: "#A3E5FF",
    flexDirection: "row",
    justifyContent: "space-evenly",
    margin: "1%",
    marginTop: "2%",
    alignItems: "center",
    width: "40%",
    height: 80,
  },

  ScrolIcon22: {
    borderRadius: 20,
    borderColor: "#A3E5FF",
    borderWidth: 2,
    display: "flex",
    backgroundColor: "#A3E5FF",
    flexDirection: "row",
    justifyContent: "space-evenly",
    margin: "1%",
    alignItems: "center",
    width: "15%",
    height: 30,
  },

  ScrolIcon3: {
    borderRadius: 20,
    display: "flex",
    backgroundColor: "#A3E5FF",
    flexDirection: "row",
    justifyContent: "space-evenly",
    margin: "1%",
    marginTop: "2%",
    alignItems: "center",
    width: "25%",
    height: 80,
  },

  ScrolIcon4: {
    borderRadius: 20,
    borderWidth: 2,
    display: "flex",
    flexDirection: "row",
    justifyContent: "space-evenly",
    margin: "1%",
    alignItems: "center",
    width: "15%",
    height: 80,
  },

  container2: {
    flex: 1,
    marginLeft: "1%",
    marginRight: "1%",
    marginBottom: "1%",
    marginTop: "1%",
  },

  scrollView: {
    backgroundColor: "#E6E6E6",
    marginHorizontal: 20,
    borderRadius: 15,
  },

  text: {
    fontSize: 42,
  },

  titleText: {
    fontSize: 25,
    fontWeight: "bold",
    alignSelf: "center",
    color: Config.color_2,
  },

  menu: {
    position: "absolute",
    flex: 1,
    alignSelf: "flex-start",
    alignItems: "center",
    justifyContent: "flex-start",
    width: "5%",
    flexDirection: "column",
    backgroundColor: Config.color_1,
    height: "100%",
  },

  container: {
    height: 100,
    width: "100%",
    alignItems: "center",
  },

  Image: {
    height: 80,
    width: 90,
    alignItems: "center",
    marginTop: "1%",
  },

  input: {
    backgroundColor: Config.color_1,
    borderRadius: 20,
    width: "33%",
    height: "25%",
    left: 51,
    top: "50%",
    alignItems: "center",
    justifyContent: "flex-start",
  },

  gap: {
    height: 10,
    width: 5,
  },

  field: {
    justifyContent: "center",
    borderRadius: 20,
    width: "90%",
    height: "25%",
    padding: 20,
    backgroundColor: "white",
  },

  button: {
    justifyContent: "center",
    alignItems: "center",
    borderRadius: 20,
    width: "90%",
    height: "25%",
    padding: 20,
    backgroundColor: Config.color_2,
  },
});

export default orderDelivered;
