import { StatusBar } from "expo-status-bar";
import React, { useEffect, useState } from "react";
import {
  StyleSheet,
  Text,
  View,
  Button,
  TouchableOpacity,
  Image,
  SafeAreaView,
  TextInput,
  KeyboardAvoidingView,
  Alert,
  Platform,
} from "react-native";
import { KeyboardAwareScrollView } from "react-native-keyboard-aware-scroll-view";
import { Ionicons } from "@expo/vector-icons";
import axios from "axios";
import Config from "../../config";
import * as ImagePicker from "expo-image-picker";

const ProfilePage = ({ navigation }) => {
  const [textName, setTextName] = React.useState("");
  const [number, setNumber] = React.useState("");
  const [imageurl, setImageURL] = React.useState(null);
  var pp = require("../../assets/pp.png");
  const [image, setImage] = React.useState(null);

  useEffect(() => {
    (async () => {
      if (Platform.OS !== "web") {
        const { status } =
          await ImagePicker.requestMediaLibraryPermissionsAsync();
        if (status !== "granted") {
          alert("Sorry, we need camera roll permissions to make this work!");
        }
      }
    })();
  }, []);

  const pickImage = async () => {
    let result = await ImagePicker.launchImageLibraryAsync({
      mediaTypes: ImagePicker.MediaTypeOptions.All,
      allowsEditing: true,
      aspect: [4, 3],
      quality: 1,
      base64: true,
    });

    //console.log(result.base64);

    if (!result.cancelled) {
      setImage(result.uri);
      setImageURL(result.uri);
      //console.log("data:image/png;base64,"+ result.base64)
      // Send Image To BE
      axios
        .patch(
          Config.host +
            Config.port +
            "/api/profiles/changeimage/?token=" +
            Config.token,
          {
            image_64: "data:image/png;base64," + result.base64,
          }
        )
        .then((response) => {
          console.log(response);
          //navigation.navigate("Orders");
        })
        .catch((e) => {
          console.log(e);
        });
    }
  };

  const getProfile = async () => {
    var response = await axios.get(
      Config.host +
        Config.port +
        "/api/account/getaccountbytoken/?token=" +
        Config.token
    );

    if (response.status == 200) {
      setTextName(response.data.name);
      setNumber(response.data.mphone);
      // if (response.data[0].profile_image) {
      //   console.log(response.data[0].profile_image);
      //   var img = response.data[0].profile_image;
      //   setImageURL(img);
      console.log("Profile Get");
      //}
    } else {
     console.log("CANT Get Profile")
    }
  };

  useEffect(() => {
    getProfile();
  }, []);

  return (
    <SafeAreaView style={styles.Vstyles}>
      <View style={styles.gap}></View>
      <View style={styles.container}>
        <Image
          style={styles.Image}
          source={imageurl ? { uri: imageurl } : pp}
        />
      </View>
      <View style={styles.icon}>
        <Ionicons name="create-sharp" size={30} color="#E6E6E6" onPress={pickImage} />
      </View>

      <View style={{ position: "absolute", left: "5%", top: "5%" }}>
        <Ionicons
          size={40}
          name="arrow-back"
          onPress={() => {
            navigation.navigate("Dashboard");
          }}
        />
      </View>

      <View style={styles.inputView}>
        <Text style={styles.txt}>{" "}</Text>
        <TouchableOpacity
         style={styles.Button2} >
        <Text style={{ fontSize: 15 , textAlign:"right" ,marginRight:"10%"}}> {textName} </Text>
        </TouchableOpacity>
        <Text style={styles.txt}> {" "} </Text>
        <TouchableOpacity
         style={styles.Button2} >

        <Text style={{ fontSize: 15 , textAlign:"right" ,marginRight:"10%"}}> {number} </Text>

        </TouchableOpacity>

      </View>

      <View style={styles.buttonFrame}>
        <TouchableOpacity
          style={styles.Button}
          onPress={() => navigation.navigate("ChangeProfiePassword")}
        >
          <Text style={{ fontSize: 20 }}>  </Text>
        </TouchableOpacity>
      </View>
    </SafeAreaView>
  );
};

const styles = StyleSheet.create({
  input: {
    height: 60,
    margin: 18,
    padding: 20,
    borderRadius: 18,
    backgroundColor: "#E6E6E6",
    textAlign: "right",
    
  },

  txt: {
    textAlign: "right",
    marginRight: "10%",
    fontSize: 15,
    fontWeight: "bold",
    color: "#E6E6E6",
  },

  inputView: {
    marginTop: "1%",
    width: "70%", 
    alignSelf: "center"

  },

  keyboardview: {
    marginTop: 20,
  },

  icon: {
    marginRight: "40%",
    margin: "2%",
    alignItems: "flex-end",
  },
  gap: {
    width: 20,
    height: 20,
  },
  Button: {
    height: 40,
    margin: 18,
    borderRadius: 18,
    width: "85%",
    backgroundColor: Config.color_2,
    alignItems: "center",
    fontSize: 24,
    justifyContent: "center",
  },

  Button2: {
    height: 40,
    margin: 18,
    borderRadius: 18,
    width: "90%",
    backgroundColor: "#E6E6E6",
    fontSize: 24,
    justifyContent: "center",
  },

  container: {
    height: 200,
    width: "100%",
    alignItems: "center",
  },

  Image: {
    borderRadius: 100,
    borderWidth: 4,
    borderColor: "#E6E6E6",
    height: 200,
    width: 200,
    alignItems: "center",
  },

  Vstyles: {
    flex: 1,
    backgroundColor: "#00ADF1",
  },

  buttonFrame: {
    marginTop: "1%",
    height: 40,
    width: "50%",
    alignItems: "center",
    alignSelf: "center"
  },
});

export default ProfilePage;
