import { StatusBar } from "expo-status-bar";
import React from "react";
import { StyleSheet, Text, View } from "react-native";
import Login from "./pages/login/Login";
import { createNativeStackNavigator } from "@react-navigation/native-stack";
import { NavigationContainer } from "@react-navigation/native";

import Menu from "./pages/dashboard/Menu";
import OrderTracking from "./pages/orders/ordertracking";
import assignedPage from "./pages/orders/assignedPage";
import orderDelivered from "./pages/orders/orderDelivered";
import refusedOrder from "./pages/orders/RefusedOrder";
import assignDrivers from "./pages/orders/assignDriver";
import CompanyMenu from "./pages/dashboard/CompanyMenu";
import changePPassword from "./pages/profile/changepassword";
import MenuSuperAdmin from "./pages/dashboard/MenuSuperAdmin";
import Report from "./pages/Reports/reports-orders";
import ReportDriver from "./pages/Reports/reports-drivers";

export default function App() {





  return (
    <NavigationContainer>
      <Welcome />
    </NavigationContainer>
  );
}

const Stack = createNativeStackNavigator();
function Welcome({ navigation }) {
  return (
    <Stack.Navigator
      screenOptions={{
        headerShown: false,
        gestureEnabled: false,
      }}
      swipeEnabled={false}
    >
      <Stack.Screen name="Login" component={Login} />
      <Stack.Screen name="DriversReports" component={ReportDriver} />
      <Stack.Screen name="Reports" component={Report} />
      <Stack.Screen name="Menu" component={Menu} />
      <Stack.Screen name="MenuSuperAdmin" component={MenuSuperAdmin} />
      <Stack.Screen name="CompanyMenu" component={CompanyMenu} />
      <Stack.Screen name="ChangeProfiePassword" component={changePPassword} />
      <Stack.Screen name="orderTracking" component={OrderTracking} />
      <Stack.Screen name="assignedPage" component={assignedPage} />
      <Stack.Screen name="orderDelivered" component={orderDelivered} />
      <Stack.Screen name="refusedOrder" component={refusedOrder} />
      <Stack.Screen name="assignDrivers" component={assignDrivers} />

    </Stack.Navigator>
  );
}

