import axios from "axios";
import { StatusBar } from "expo-status-bar";
import React, { useEffect, useState } from "react";
import {
  StyleSheet,
  Text,
  View,
  TouchableOpacity,
  Image,
  SafeAreaView,
  TextInput,
  Alert,
} from "react-native";
import { Ionicons, Entypo } from "@expo/vector-icons";
import { KeyboardAwareScrollView } from "react-native-keyboard-aware-scroll-view";
import Config from "../../config";

const changePPassword = ({ navigation }) => {
  const [textName, onChangeTextName] = useState("");
  const [textpass1, onChangePass1] = useState("");
  const [textpass2, onChangePass2] = useState("");
  const [mobileNumber, setMobileNumber] = useState("");
  const [showError, setShowError] = useState(false);
  const [errorHeader, setErrorHeader] = useState("");
  const [errorBody, setErrorBody] = useState("");

  const ShowErrorFunction = (header, body) => {
    setErrorHeader(header);
    setErrorBody(body);
    setShowError(true);
  };

  const getProfile = async () => {
    var response = await axios.get(
      Config.host +
        Config.port +
        "/api/profiles/getprofile/?token=" +
        Config.token
    );
  };

  const changePassword = async () => {
    var response = await axios
      .patch(
        Config.host +
          Config.port +
          "/api/account/change_password/" +
          Config.token +
          "/",
        {
          password: textpass1,
          password2: textpass2,
        },

        {
          headers: { "Content-Type": "application/json" },
        },

        {
          auth: {
            username: mobileNumber,
            password: textName,
          },
        }
      )
      .then((resp) => {
        console.log(resp);
        ShowErrorFunction("", "Password changed sucessfully!");
        // navigation.navigate("ProfilePage");
      })

      .catch((err) => {
        if (err.response.status == 400){
          if(err.response.data.password== "This field may not be null." || err.response.data.password2== "This field may not be blank." ){
            ShowErrorFunction("Failed", "The password field may not be blank");
          }
          else{
          ShowErrorFunction("Failed", err.response.data.password);
          }
        }
        else{
        // Alert.alert("Something Went Wrong Please try again!")
        ShowErrorFunction("Failed", "Something Went Wrong, Please try again!");
        }
        // console.log("CHANGE PASS ERR")
        console.log(err.response);
      });
  };

  useEffect(() => {
    getProfile();
  }, []);

  return (
    <SafeAreaView style={styles.Vstyles}>
      <KeyboardAwareScrollView style={styles.keyboardview}>
        <View style={styles.container}>
          <Image
            style={styles.Image}
            source={require("../../assets/logo-01.png")}
          />
        </View>

        <View style={{ position: "absolute", left: "5%", top: "5%" }}>
          <Ionicons
            size={40}
            name="arrow-back"
            onPress={() => {
              navigation.navigate("ProfilePage");
            }}
          />
        </View>

        <View style={{height: 150}}></View>

        <View style={styles.inputView}>
          <TextInput
            style={styles.input}
            onChangeText={onChangeTextName}
            value={textName}
            placeholder="  "
            textAlign="right"
            secureTextEntry={true}
          />
          <TextInput
            style={styles.input}
            onChangeText={onChangePass1}
            value={textpass1}
            placeholder="  "
            textAlign="right"
            secureTextEntry={true}
          />

          <TextInput
            style={styles.input}
            onChangeText={onChangePass2}
            value={textpass2}
            placeholder="    "
            textAlign="right"
            secureTextEntry={true}
          />
        </View>

        <View style={styles.buttonFrame}>
          <TouchableOpacity style={styles.Button} onPress={changePassword}>
            <Text style={{ fontSize: 20 }}></Text>
          </TouchableOpacity>
        </View>

        {showError ? (
          <View
            style={{
              backgroundColor: Config.color_2,
              position: "absolute",
              alignSelf: "center",
              marginTop: "20%",
              width: 400,
              height: 200,
              borderRadius: 20,
              // alignItems: "center",
              // justifyContent: "center",
              zIndex: 888,
            }}
          >
            <View
              style={{
                flexDirection: "row",
                width: "100%",
                justifyContent: "space-between",
              }}
            >
              <Image
                style={{
                  width: 34,
                  height: 30,
                  marginTop: "5%",
                  marginLeft: "5%",
                }}
                source={require("../../assets/logo-01.png")}
              />

              <View
                style={{
                  alignSelf: "flex-end",
                  marginTop: "5%",
                  marginRight: "5%",
                }}
              >
                <Entypo
                  name="cross"
                  size={24}
                  color="black"
                  onPress={() => {
                    setShowError(false);
                  }}
                />
              </View>
            </View>

            <Text
              style={{
                alignSelf: "center",
                fontWeight: "bold",
                color: "red",
                fontSize: 24,
              }}
            >
              {errorHeader}
            </Text>
            <View style={{ margin: "5%" }}>
              <Text
                style={{
                  alignSelf: "center",
                  fontWeight: "bold",
                  fontSize: 16,
                }}
              >
                {errorBody}
              </Text>
            </View>
            <View style={{ height: 5 }}></View>
          </View>
        ) : (
          <View></View>
        )}
      </KeyboardAwareScrollView>
    </SafeAreaView>
  );
};

const styles = StyleSheet.create({
  input: {
    height: 60,
    margin: 18,
    padding: 20,
    borderRadius: 18,
    backgroundColor: "#E6E6E6",
  },

  inputView: {
    marginTop: "1%",
    width: "70%",
    alignSelf: "center",
  },

  keyboardview: {
    marginTop: 20,
  },

  Button: {
    height: 60,
    margin: 18,
    padding: 20,
    borderRadius: 18,
    width: "85%",
    backgroundColor: Config.color_2,
    alignItems: "center",
    fontSize: 24,
    justifyContent: "center",
  },

  container: {
    height: 100,
    width: "100%",
    alignItems: "center",
  },

  Image: {
    height: 100,
    width: 100,
    alignItems: "center",
  },

  Vstyles: {
    flex: 1,
    backgroundColor: "#00ADF1",
    justifyContent: "space-evenly",
  },

  buttonFrame: {
    marginTop: "1%",
    height: 40,
    width: "50%",
    alignItems: "center",
    alignSelf: "center",
  },
});

export default changePPassword;
