import React, { useEffect, useState } from "react";
import {
  Image,
  SafeAreaView,
  ScrollView,
  StyleSheet,
  Text,
  TextInput,
  TouchableOpacity,
  View,
} from "react-native";
import Config from "../../config";
import {
  Ionicons,
  FontAwesome,
  MaterialCommunityIcons,
} from "@expo/vector-icons";
import {
  withScriptjs,
  withGoogleMap,
  GoogleMap,
  Marker,
} from "react-google-maps";
import axios from "axios";
import MarkerWithLabel from "react-google-maps/lib/components/addons/MarkerWithLabel";
import { StatusBar } from "expo-status-bar";
import Loader from "react-loader-spinner";
import moment, { now } from "moment";
import Sidebar from "../dashboard/Sidebar";
import { NavigationContainer } from "@react-navigation/native";
import CalendarPicker from "react-native-calendar-picker";
import { Svg, Text as TextSVG } from "react-native-svg";
import {
  LineChart,
  BarChart,
  PieChart,
  ProgressChart,
  ContributionGraph,
  StackedBarChart,
} from "react-native-chart-kit";

const Statistics = ({ navigation }) => {
  const [loading, setLoading] = useState(false);
  // const [filterColor, setfilterColor] = useState(Config.color_1);
  const [selectedStartDate, onStartDateChange] = useState(null);
  const [selectedEndDate, onEndDateChange] = useState(null);
  const [chooseStartDate, setChooseStartDate] = useState(false);
  const [chooseEndDate, setChooseEndDate] = useState(false);
  const [graphName, setGraphName] = useState(
    "Please choose start and end date then choose the statistics type!"
  );
  const [choosenSDates, setChoosenSDates] = useState("");
  const [choosenEDates, setChoosenEDates] = useState("");
  const [graphNameColor, setgraphNameColor] = useState(Config.color_1);
  const date = new Date().getDate();
  const [yAxisSuffixName, setyAxisSuffixName] = useState("");
  const [dataset, setdataset] = useState([0]);
  const [labelsSet, setlabelsSet] = useState([0]);
  const [Mapper, setMapper] = useState([[[0], [0]]]);
  const [numberOfPages, setNumberOfPages] = useState(1);
  const [Mapper2, setMapper2] = useState([[]]);
  const [charttype, setcharttype] = useState("Line");
  const [adminBranch, setAdminBranch] = useState("");

  //Pie Chart Configs
  const chartConfig = {
    backgroundGradientFrom: "#1E2923",
    backgroundGradientFromOpacity: 0,
    backgroundGradientTo: "#08130D",
    backgroundGradientToOpacity: 0.5,
    color: (opacity = 1) => `rgba(26, 255, 146, ${opacity})`,
    strokeWidth: 2, // optional, default 3
    barPercentage: 0.5,
    useShadowColorFromDataset: false, // optional
  };

  function getRandomColor() {
    var letters = "0123456789ABCDEF";
    var color = "#";
    for (var i = 0; i < 6; i++) {
      color += letters[Math.floor(Math.random() * 16)];
    }
    return color;
  }
  const data = [
    {
      name: "Seoul",
      population: 21500000,
      color: "rgba(131, 167, 234, 1)",
      legendFontColor: "#7F7F7F",
      legendFontSize: 15,
    },
    {
      name: "Toronto",
      population: 2800000,
      color: "#F00",
      legendFontColor: "#7F7F7F",
      legendFontSize: 15,
    },
    {
      name: "Beijing",
      population: 527612,
      color: "red",
      legendFontColor: "#7F7F7F",
      legendFontSize: 15,
    },
    {
      name: "New York",
      population: 8538000,
      color: "#ffffff",
      legendFontColor: "#7F7F7F",
      legendFontSize: 15,
    },
    {
      name: "Moscow",
      population: 11920000,
      color: "rgb(0, 0, 255)",
      legendFontColor: "#7F7F7F",
      legendFontSize: 15,
    },
  ];
  //______________________________________________________________//

  // const setdriverdata = (drivers) => {
  //   var dname = [""];
  //   var dmaxorders= [0];

  //   for (var i=0 ; i< drivers.length; i++){
  //     dname.push(drivers[i].driver_name)
  //     dmaxorders.push(drivers[i].driver_max_orders)
  //   }
  //   setdataset(dmaxorders);
  //   setlabelsSet(dname);

  // }

  const setOrderdata = (ordersdata) => {
    var Mapper22 = [];
    Mapper22.push(["Date", "Number of orders"]);

    var allLabels = [];
    setyAxisSuffixName("");
    var threshold = 15;
    var numberOfPages = 1;
    var DataLength = ordersdata.length;
    if (DataLength / 3 > threshold) {
      numberOfPages = Math.ceil(DataLength / threshold);
      setNumberOfPages(numberOfPages);
    }

    var xAxisTemp = [""];
    var YAxisTemp = [0];
    for (var i = 0; i < DataLength; i = i + 3) {
      xAxisTemp.push(
        ordersdata[i + 2].split("T")[0].split("-")[0] +
          "-" +
          ordersdata[i + 2].split("T")[0].split("-")[1] +
          "-" +
          ordersdata[i + 2].split("T")[0].split("-")[2]
      );
      YAxisTemp.push(ordersdata[i + 1]);
      Mapper22.push([
        ordersdata[i + 2].split("T")[0].split("-")[0] +
          "-" +
          ordersdata[i + 2].split("T")[0].split("-")[1] +
          "-" +
          ordersdata[i + 2].split("T")[0].split("-")[2],
        ordersdata[i + 1],
      ]);
    }

    allLabels.push([xAxisTemp, YAxisTemp]);
    setMapper2(Mapper22);
    setMapper(allLabels);
  };

  const setDriverOrderdata = (ordersdata) => {
    // TODO: Check for the driver's branch and filter drivers which have the same branch of the admin ==>  if no branch ok
    var Mapper22 = [];
    Mapper22.push(["Driver Name", "Number of orders"]);
    var allLabels = [];
    setyAxisSuffixName("");
    var threshold = 10;
    var numberOfPages = 1;
    var DataLength = ordersdata.length;
    if (DataLength / 4 > threshold) {
      numberOfPages = Math.ceil(DataLength / threshold);
    }
    setNumberOfPages(numberOfPages);
    var xAxisTemp = [""];
    var YAxisTemp = [0];
    for (var i = 0; i < DataLength; i = i + 4) {
      xAxisTemp.push(ordersdata[i]);
      YAxisTemp.push(ordersdata[i + 2]);
      Mapper22.push([ordersdata[i], ordersdata[i + 2]]);
    }
    setMapper2(Mapper22);
    allLabels.push([xAxisTemp, YAxisTemp]);
    setMapper(allLabels);
  };

  const setDriverTimeOrderdata = (ordersdata) => {
    // TODO: Check for the driver's branch and filter drivers which have the same branch of the admin ==>  if no branch ok
    var Mapper22 = [];
    Mapper22.push(["Driver Name", "Time"]);

    var allLabels = [];
    setyAxisSuffixName("H");
    var threshold = 10;
    var numberOfPages = 1;
    var DataLength = ordersdata.length;
    if (DataLength / 4 > threshold) {
      numberOfPages = Math.ceil(DataLength / threshold);
    }
    setNumberOfPages(numberOfPages);
    var xAxisTemp = [""];
    var YAxisTemp = [0];
    for (var i = 0; i < DataLength; i = i + 4) {
      xAxisTemp.push(ordersdata[i]);
      YAxisTemp.push(ordersdata[i + 2]);
      Mapper22.push([ordersdata[i], ordersdata[i + 2]]);
    }
    setMapper2(Mapper22);
    allLabels.push([xAxisTemp, YAxisTemp]);
    setMapper(allLabels);
  };

  const setCompaniesOrderdata = (ordersdata) => {
    var Mapper22 = [];
    Mapper22.push(["Company Name", "Number of orders"]);

    var allLabels = [];
    setyAxisSuffixName("");
    var threshold = 10;
    var numberOfPages = 1;
    var DataLength = ordersdata.length;
    if (DataLength / 4 > threshold) {
      numberOfPages = Math.ceil(DataLength / threshold);
    }

    setNumberOfPages(numberOfPages);
    var xAxisTemp = [""];
    var YAxisTemp = [0];
    for (var i = 0; i < DataLength; i = i + 4) {
      xAxisTemp.push(ordersdata[i]);
      YAxisTemp.push(ordersdata[i + 2]);
      Mapper22.push([ordersdata[i], ordersdata[i + 2]]);
    }
    setMapper2(Mapper22);
    allLabels.push([xAxisTemp, YAxisTemp]);
    setMapper(allLabels);
  };

  useEffect(() => {
    console.log("");
    //If Admin --> Get Admin branch
    if (Config.accountType == "A") {
      axios
        .get(
          Config.host +
            Config.port +
            "/api/account/getbranchbytoken/?token=" +
            Config.token
        )
        .then((response) => {
          console.log(response.data);
          setAdminBranch(response.data);
        })
        .catch((e) => {
          console.log(e);
        });
    }

    // if(Config.accountType == "Co"){

    // }
  }, []);

  const getGraphData = (graphType) => {
    if (
      selectedStartDate &&
      selectedEndDate &&
      selectedEndDate >= selectedStartDate
    ) {
      setgraphNameColor("black");
      setChoosenSDates("    From: " + selectedStartDate.format("YYYY-MM-DD"));
      setChoosenEDates("    To: " + selectedEndDate.format("YYYY-MM-DD"));
      var duration = moment.duration(selectedEndDate.diff(selectedStartDate));
      var durationDays = duration.asDays();
      console.log("Duration");
      console.log(durationDays);
      setLoading(true);
      if (Config.accountType == "SA") {
        if (graphType == "O") {
          var daysplit = durationDays;
          var dif = 0;

          if (durationDays > 20) {
            if (durationDays % 20 != 0) {
              dif = durationDays % 20;
              daysplit = (durationDays - dif) / 20 + 1;
            } else {
              daysplit = durationDays / 20;
            }
          } else {
            daysplit = 1;
          }

          console.log("Daysplit");
          console.log(daysplit);
          console.log(
            Config.host +
              Config.port +
              "/api/orders/getcountorderbetweendates/?start_date=" +
              selectedStartDate.format("YYYY-MM-DD") +
              "&end_date=" +
              selectedEndDate.format("YYYY-MM-DD") +
              "&days=1"
          );

          axios
            .get(
              Config.host +
                Config.port +
                "/api/orders/getcountorderbetweendates/?start_date=" +
                selectedStartDate.format("YYYY-MM-DD").toString() +
                "&end_date=" +
                selectedEndDate.format("YYYY-MM-DD").toString() +
                "&days=1"
            )
            .then((response) => {
              console.log("RESPONSE");
              console.log(response.data);
              setOrderdata(response.data);
              setLoading(false);
            })
            .catch((e) => {
              console.log(e);
              setLoading(false);
            });
        } else if (graphType == "D") {
          axios
            .get(
              Config.host +
                Config.port +
                "/api/orders/getcountorderbetweendatesdriver/?start_date=" +
                selectedStartDate.format("YYYY-MM-DD").toString() +
                "&end_date=" +
                selectedEndDate.format("YYYY-MM-DD").toString() +
                "&days=0"
            )
            .then((response) => {
              console.log("RESPONSE");
              console.log(response.data);
              setDriverOrderdata(response.data);
              setLoading(false);
            })
            .catch((e) => {
              console.log(e);
              setLoading(false);
            });
        } else if (graphType == "T") {
          axios
            .get(
              Config.host +
                Config.port +
                "/api/orders/getdeliverytimebetweendatesdriver/?start_date=" +
                selectedStartDate.format("YYYY-MM-DD").toString() +
                "&end_date=" +
                selectedEndDate.format("YYYY-MM-DD").toString() +
                "&days=0"
            )
            .then((response) => {
              console.log("RESPONSE");
              console.log(response.data);
              setDriverTimeOrderdata(response.data);
              setLoading(false);
            })
            .catch((e) => {
              console.log(e);
              setLoading(false);
            });
        } else if (graphType == "C") {
          axios
            .get(
              Config.host +
                Config.port +
                "/api/orders/getcountorderbetweendatescompany/?start_date=" +
                selectedStartDate.format("YYYY-MM-DD").toString() +
                "&end_date=" +
                selectedEndDate.format("YYYY-MM-DD").toString() +
                "&days=0"
            )
            .then((response) => {
              console.log("RESPONSE");
              console.log(response.data);
              setCompaniesOrderdata(response.data);
              setLoading(false);
            })
            .catch((e) => {
              console.log(e);
              setLoading(false);
            });
        }
      } else if (Config.accountType == "A") {
        if (graphType == "O") {
          var daysplit = durationDays;
          var dif = 0;

          if (durationDays > 20) {
            if (durationDays % 20 != 0) {
              dif = durationDays % 20;
              daysplit = (durationDays - dif) / 20 + 1;
            } else {
              daysplit = durationDays / 20;
            }
          } else {
            daysplit = 1;
          }

          console.log("Daysplit");
          console.log(daysplit);
          console.log(
            Config.host +
              Config.port +
              "/api/orders/getcountorderbetweendatesforbranch/?start_date=" +
              selectedStartDate.format("YYYY-MM-DD") +
              "&end_date=" +
              selectedEndDate.format("YYYY-MM-DD") +
              "&days=1" +
              "&branch=" +
              adminBranch
          );

          axios
            .get(
              Config.host +
                Config.port +
                "/api/orders/getcountorderbetweendatesforbranch/?start_date=" +
                selectedStartDate.format("YYYY-MM-DD").toString() +
                "&end_date=" +
                selectedEndDate.format("YYYY-MM-DD").toString() +
                "&days=1" +
                "&branch=" +
                adminBranch
            )
            .then((response) => {
              console.log("RESPONSE");
              console.log(response.data);
              setOrderdata(response.data);
              setLoading(false);
            })
            .catch((e) => {
              console.log(e);
              setLoading(false);
            });
        } else if (graphType == "D") {
          axios
            .get(
              Config.host +
                Config.port +
                "/api/orders/getcountorderbetweendatesdriverforbranch/?start_date=" +
                selectedStartDate.format("YYYY-MM-DD").toString() +
                "&end_date=" +
                selectedEndDate.format("YYYY-MM-DD").toString() +
                "&days=0" +
                "&branch=" +
                adminBranch
            )
            .then((response) => {
              console.log("RESPONSE");
              console.log(response.data);
              setDriverOrderdata(response.data);
              setLoading(false);
            })
            .catch((e) => {
              console.log(e);
              setLoading(false);
            });
        } else if (graphType == "T") {
          axios
            .get(
              Config.host +
                Config.port +
                "/api/orders/getdeliverytimebetweendatesdriverforbranch/?start_date=" +
                selectedStartDate.format("YYYY-MM-DD").toString() +
                "&end_date=" +
                selectedEndDate.format("YYYY-MM-DD").toString() +
                "&days=0" +
                "&branch=" +
                adminBranch
            )
            .then((response) => {
              console.log("RESPONSE");
              console.log(response.data);
              setDriverTimeOrderdata(response.data);
              setLoading(false);
            })
            .catch((e) => {
              console.log(e);
              setLoading(false);
            });
        } else if (graphType == "C") {
          axios
            .get(
              Config.host +
                Config.port +
                "/api/orders/getcountorderbetweendatescompany/?start_date=" +
                selectedStartDate.format("YYYY-MM-DD").toString() +
                "&end_date=" +
                selectedEndDate.format("YYYY-MM-DD").toString() +
                "&days=0"
            )
            .then((response) => {
              console.log("RESPONSE");
              console.log(response.data);
              setCompaniesOrderdata(response.data);
              setLoading(false);
            })
            .catch((e) => {
              console.log(e);
              setLoading(false);
            });
        }
      } else if (Config.accountType == "Co") {
        if (graphType == "D") {
          var companyID = -1;
          axios
            .get(
              Config.host +
                Config.port +
                "/api/account/getaccountbytoken/?token=" +
                Config.token
            )
            .then((response) => {
              axios
                .get(
                  Config.host + Config.port + "/api/companies/getallcompanies/"
                )
                .then((companies_response) => {
                  console.log(companies_response.data);
                  for (var i in companies_response.data) {
                    console.log("COMPANIES RESPONSE i Loop");
                    console.log(companies_response.data[i].company_name);
                    console.log(response.data.name);
                    if (
                      companies_response.data[i].company_name ==
                      response.data.name
                    ) {
                      companyID = companies_response.data[i].company_number;
                    }
                  }

                  axios
                    .get(
                      Config.host +
                        Config.port +
                        "/api/orders/getcountorderbetweendatesdriverforcompany/?start_date=" +
                        selectedStartDate.format("YYYY-MM-DD").toString() +
                        "&end_date=" +
                        selectedEndDate.format("YYYY-MM-DD").toString() +
                        "&days=0" +
                        "&company=" +
                        companyID
                    )
                    .then((response2) => {
                      console.log("RESPONSE");
                      console.log(response2.data);
                      setDriverOrderdata(response2.data);
                      setLoading(false);
                    })
                    .catch((e) => {
                      console.log(e);
                      setLoading(false);
                    });
                });
            });
        } else if (graphType == "T") {
          var companyID = -1;
          axios
            .get(
              Config.host +
                Config.port +
                "/api/account/getaccountbytoken/?token=" +
                Config.token
            )
            .then((response) => {
              axios
                .get(
                  Config.host + Config.port + "/api/companies/getallcompanies/"
                )
                .then((companies_response) => {
                  console.log(companies_response.data);
                  for (var i in companies_response.data) {
                    console.log("COMPANIES RESPONSE i Loop");
                    console.log(companies_response.data[i].company_name);
                    console.log(response.data.name);
                    if (
                      companies_response.data[i].company_name ==
                      response.data.name
                    ) {
                      companyID = companies_response.data[i].company_number;
                    }
                  }

                  axios
                    .get(
                      Config.host +
                        Config.port +
                        "/api/orders/getdeliverytimebetweendatesdriverforcompany/?start_date=" +
                        selectedStartDate.format("YYYY-MM-DD").toString() +
                        "&end_date=" +
                        selectedEndDate.format("YYYY-MM-DD").toString() +
                        "&days=0" +
                        "&company=" +
                        companyID
                    )
                    .then((response2) => {
                      console.log("RESPONSE");
                      console.log(response2.data);
                      setDriverTimeOrderdata(response2.data);
                      setLoading(false);
                    })
                    .catch((e) => {
                      console.log(e);
                      setLoading(false);
                    });
                });
            });
        }
      }
    } else {
      setGraphName("Please choose a correct start and end dates!");
      //setChoosenDates("")
      setgraphNameColor("red");
      setdataset([0, 0, 0, 0]);
      setlabelsSet([0, 0, 0, 0]);
    }
  };

  const chooseDateShow = (datetype) => {
    if (datetype == "S") {
      setChooseStartDate(true);
    }
    if (datetype == "E") {
      setChooseEndDate(true);
    }
  };

  return (
    <View style={{ flex: 1, backgroundColor: "#FFFF" }}>
      <View style={styles.gap}></View>
      <View
        style={{
          flexDirection: "row",
          alignSelf: "flex-start",
          paddingLeft: "7%",
        }}
      >
        <Image
          style={{ width: 90, height: 80 }}
          source={require("../../assets/logo-01.png")}
        />
        <View style={styles.gap}></View>
        <Text
          style={{
            paddingLeft: 10,
            color: Config.color_1,
            fontWeight: "bold",
            alignSelf: "center",
            fontSize: 24,
          }}
        >
          Statistics Page
        </Text>
      </View>

      <Sidebar navigation={navigation} />

      <View
        style={{
          flexDirection: "row",
          alignSelf: "flex-end",
          paddingRight: "5%",
        }}
      >
        <View style={{ width: 40 }} />

        <View style={styles.gap}></View>
        <View style={styles.gap}></View>
      </View>
      {loading ? (
        <View
          style={{
            width: "90%",
            height: "75%",
            justifyContent: "center",
            alignContent: "center",
            alignSelf: "center",
            alignItems: "center",
          }}
        >
          <Loader color={Config.color_1} />
        </View>
      ) : (
        <>
          <View style={styles.scrollcontainer2}>
            <TouchableOpacity
              style={[
                styles.ScrolIcon22,
                { borderColor: "#E6E6E6" },
                { backgroundColor: "#E6E6E6" },
              ]}
              onPress={() => {
                // setfilterColor("#00F171");
                setcharttype("Line");
              }}
            >
              <Text style={{ fontSize: 15, width: "60%", alignSelf: "center" }}>
                LineChart {"\n"}
              </Text>
              <Ionicons name="analytics" size={25} color="black" />
            </TouchableOpacity>

            <TouchableOpacity
              style={[
                styles.ScrolIcon22,
                { borderColor: "#E6E6E6" },
                { backgroundColor: "#E6E6E6" },
              ]}
              onPress={() => {
                setcharttype("Bar");
              }}
            >
              <Text style={{ fontSize: 15, width: "60%", alignSelf: "center" }}>
                BarChart{"\n"}
              </Text>
              <Ionicons name="bar-chart" size={25} color="black" />
            </TouchableOpacity>
          </View>

          <View style={styles.scrollcontainer2}>
            <TouchableOpacity
              style={[
                styles.ScrolIcon22,
                { borderColor: Config.color_1 },
                { backgroundColor: Config.color_1 },
              ]}
              onPress={() => {
                chooseDateShow("S");
                setChooseEndDate(false);
              }}
            >
              <Text style={{ fontSize: 15, width: "60%", alignSelf: "center" }}>
                Start Date{"\n"}
              </Text>
              <Ionicons name="calendar" size={24} color="black" />
            </TouchableOpacity>

            <TouchableOpacity
              style={[
                styles.ScrolIcon22,
                { borderColor: "#00DFF1" },
                { backgroundColor: "#00DFF1" },
              ]}
              onPress={() => {
                chooseDateShow("E");
                setChooseStartDate(false);
              }}
            >
              <Text style={{ fontSize: 15, width: "60%", alignSelf: "center" }}>
                End Date{"\n"}
              </Text>
              <Ionicons name="calendar" size={24} color="black" />
            </TouchableOpacity>
            {Config.accountType != "Co" ? (
              <TouchableOpacity
                style={[
                  styles.ScrolIcon22,
                  { borderColor: "#00F1D4" },
                  { backgroundColor: "#00F1D4" },
                ]}
                onPress={() => {
                  // setfilterColor("#00F171");
                  setChooseEndDate(false);
                  setChooseStartDate(false);
                  setNumberOfPages(1);
                  setGraphName("Total Number of Orders Graph");
                  getGraphData("O");
                }}
              >
                <Text
                  style={{ fontSize: 15, width: "60%", alignSelf: "center" }}
                >
                  Orders {"\n"}
                </Text>
                <Ionicons name="restaurant" size={25} color="black" />
              </TouchableOpacity>
            ) : (
              <View></View>
            )}
            <TouchableOpacity
              style={[
                styles.ScrolIcon22,
                { borderColor: "#00F17C" },
                { backgroundColor: "#00F17C" },
              ]}
              onPress={() => {
                // setfilterColor("#9DF100");
                setChooseEndDate(false);
                setChooseStartDate(false);
                setNumberOfPages(1);
                setGraphName("Drivers' Total Number of Orders Graph");
                getGraphData("D");
              }}
            >
              <Text style={{ fontSize: 15, width: "60%", alignSelf: "center" }}>
                Drivers' Orders {"\n"}
              </Text>
              <Ionicons name="person" size={25} color="black" />
            </TouchableOpacity>

            <TouchableOpacity
              style={[
                styles.ScrolIcon22,
                { borderColor: "#ECE91A" },
                { backgroundColor: "#ECE91A" },
              ]}
              onPress={() => {
                // setfilterColor("#DBF100");
                setChooseEndDate(false);
                setChooseStartDate(false);
                setNumberOfPages(1);
                setGraphName("Drivers' average delivery time graph");
                getGraphData("T");
              }}
            >
              <Text style={{ fontSize: 15, width: "60%", alignSelf: "center" }}>
                Drivers' Avg. Delivery Time {"\n"}
              </Text>
              <Ionicons name="time" size={25} color="black" />
            </TouchableOpacity>

            {Config.accountType == "SA" ? (
              <TouchableOpacity
                style={[
                  styles.ScrolIcon22,
                  { borderColor: Config.color_2 },
                  { backgroundColor: Config.color_2 },
                ]}
                onPress={() => {
                  // setfilterColor(Config.color_2);
                  setChooseEndDate(false);
                  setChooseStartDate(false);
                  setNumberOfPages(1);
                  setGraphName("Companies Total Number of Orders Graph");
                  getGraphData("C");
                }}
              >
                <Text
                  style={{ fontSize: 15, width: "60%", alignSelf: "center" }}
                >
                  Companies {"\n"}
                </Text>
                <FontAwesome name="building" size={20} color="black" />
              </TouchableOpacity>
            ) : (
              <View></View>
            )}
          </View>

          {chooseStartDate ? (
            <View
              style={{
                justifyContent: "center",
                alignItems: "center",
                alignSelf: "center",
                marginTop: "3%",
              }}
            >
              <CalendarPicker
                selectedDayColor={Config.color_1}
                onDateChange={onStartDateChange}
                width="1000"
                height="400"
                // maxDate= {date}
              />
              <TouchableOpacity
                style={[styles.datebutton]}
                onPress={() => {
                  if (selectedStartDate) {
                    setChooseStartDate(false);
                    setChoosenSDates(
                      "    From: " + selectedStartDate.format("YYYY-MM-DD")
                    );
                  }
                }}
              >
                <Text
                  style={{ fontSize: 15, width: "60%", alignSelf: "center" }}
                >
                  Choose Start Date{"\n"}
                </Text>
                <Ionicons name="checkmark" size={24} color="black" />
              </TouchableOpacity>
            </View>
          ) : chooseEndDate ? (
            <View
              style={{
                justifyContent: "center",
                alignItems: "center",
                alignSelf: "center",
                marginTop: "3%",
              }}
            >
              <CalendarPicker
                selectedDayColor={Config.color_1}
                onDateChange={onEndDateChange}
                width="1000"
                height="400"
              />

              <TouchableOpacity
                style={[styles.datebutton]}
                onPress={() => {
                  if (selectedEndDate) {
                    setChooseEndDate(false);
                    setChoosenEDates(
                      "    To: " + selectedEndDate.format("YYYY-MM-DD")
                    );
                  }
                }}
              >
                <Text
                  style={{ fontSize: 15, width: "60%", alignSelf: "center" }}
                >
                  Choose End Date{"\n"}
                </Text>
                <Ionicons name="checkmark" size={24} color="black" />
              </TouchableOpacity>
            </View>
          ) : (
            <View style={{ marginLeft: "3%" }}>
              <View style={styles.gap}></View>
              <Text
                style={{
                  marginTop: "1%",
                  fontSize: 20,
                  alignSelf: "center",
                  color: graphNameColor,
                  fontWeight: "bold",
                }}
              >
                {graphName}
                <Text style={{ color: Config.color_1 }}> {choosenSDates}</Text>
                <Text style={{ color: "#00DFF1" }}> {choosenEDates}</Text>
              </Text>

              <ScrollView style={styles.container2}>
                <ScrollView
                  style={styles.container2}
                  horizontal={true}
                  bounces={true}
                >
                  {Mapper.map((Mapper) => (
                    <View key={Mapper}>
                      <View style={styles.graph}>
                        {charttype == "Line" ? (
                          <LineChart
                            data={{
                              labels: Mapper[0],
                              datasets: [
                                {
                                  data: Mapper[1],
                                },
                              ],
                            }}
                            width={1300 * numberOfPages}
                            height={400}
                            yAxisLabel=""
                            yAxisSuffix={yAxisSuffixName}
                            // verticalLabelRotation={360}
                            yAxisInterval={1} // optional, defaults to 1
                            chartConfig={{
                              backgroundColor: "#0000",
                              backgroundGradientFrom: Config.color_1,
                              backgroundGradientTo: Config.color_2,
                              decimalPlaces: 2, // optional, defaults to 2dp
                              color: (opacity = 0) =>
                                `rgba(0, 0, 0, ${opacity})`,
                              labelColor: (opacity = 1) =>
                                `rgba(0, 0, 0, ${opacity})`,

                              style: {
                                borderRadius: 16,
                              },
                              propsForDots: {
                                r: "6",
                                strokeWidth: "2",
                                stroke: "#ffa726",
                              },

                              // fillShadowGradient: "#00F1DF",
                              // fillShadowGradientOpacity: 1,
                            }}
                            bezier
                            style={{
                              marginVertical: 8,
                              borderRadius: 16,
                            }}
                            renderDotContent={({ x, y, index }) => {
                              return (
                                <TextSVG
                                  key={index}
                                  x={x + 25}
                                  y={y}
                                  fill="black"
                                  fontSize="16"
                                  fontWeight="normal"
                                  textAnchor="middle"
                                >
                                  {Mapper[1][index].toFixed(2)}
                                </TextSVG>
                              );
                            }}
                          />
                        ) : (
                          <View></View>
                        )}

                        {charttype == "Bar" ? (
                          <BarChart
                            data={{
                              labels: Mapper[0],
                              datasets: [
                                {
                                  data: Mapper[1],
                                },
                              ],
                            }}
                            width={1300 * numberOfPages}
                            height={400}
                            yAxisLabel=""
                            yAxisSuffix={yAxisSuffixName}
                            // verticalLabelRotation={360}
                            yAxisInterval={1} // optional, defaults to 1
                            chartConfig={{
                              backgroundColor: "#0000",
                              backgroundGradientFrom: Config.color_1,
                              backgroundGradientTo: Config.color_2,
                              decimalPlaces: 2, // optional, defaults to 2dp
                              color: (opacity = 0) =>
                                `rgba(0, 0, 0, ${opacity})`,
                              labelColor: (opacity = 1) =>
                                `rgba(0, 0, 0, ${opacity})`,

                              style: {
                                borderRadius: 16,
                              },
                              propsForDots: {
                                r: "6",
                                strokeWidth: "2",
                                stroke: "#ffa726",
                              },

                              // fillShadowGradient: "#00F1DF",
                              // fillShadowGradientOpacity: 1,
                            }}
                            bezier
                            style={{
                              marginVertical: 8,
                              borderRadius: 16,
                            }}
                            renderDotContent={({ x, y, index }) => {
                              return (
                                <TextSVG
                                  key={index}
                                  x={x + 25}
                                  y={y}
                                  fill="black"
                                  fontSize="16"
                                  fontWeight="normal"
                                  textAnchor="middle"
                                >
                                  {Mapper[1][index].toFixed(2)}
                                </TextSVG>
                              );
                            }}
                          />
                        ) : (
                          <View></View>
                        )}

                        {/* {charttype == "Pie" ? (
                          <PieChart
                            data={
                              {
                                name: Mapper[1],
                                population: Mapper[0],
                                color: getRandomColor(),
                                legendFontColor: "#7F7F7F",
                                legendFontSize: 15,
                              }
                            }
                            // data={data}
                            width={1300 * numberOfPages}
                            height={500}
                            chartConfig={chartConfig}
                            accessor={"population"}
                            backgroundColor={"transparent"}
                            paddingLeft={"15"}
                            center={[10, 50]}
                            absolute
                          />
                        ) : (
                          <View></View>
                        )} */}
                      </View>
                      <View style={styles.gap}></View>
                    </View>
                  ))}
                </ScrollView>

                {/* <View style={{ marginTop: "3%", marginLeft: "5%" }}>
                  {Mapper2.map((Mapper2) => (
                    <View
                      style={{
                        width: "90%",
                        alignSelf: "center",
                        flexDirection: "row",
                      }}
                    >
                      <Text
                        style={{
                          width: "50%",
                          color: "black",
                          borderColor: "black",
                          borderWidth: 1,
                          fontWeight: "bold",
                        }}
                      >
                        {Mapper2[0]}
                      </Text>

                      <Text
                        style={{
                          width: "50%",
                          color: "black",
                          borderColor: "black",
                          borderWidth: 1,
                          fontWeight: "bold",
                        }}
                      >
                        {Mapper2[1]}
                      </Text>
                    </View>
                  ))}

                  <View style={styles.gap}></View>
                  <View style={styles.gap}></View>
                  <View style={styles.gap}></View>
                </View> */}
              </ScrollView>
            </View>
          )}
        </>
      )}
    </View>
  );
};

var styles = StyleSheet.create({
  containertable: {
    flex: 1,
    padding: 16,
    paddingTop: 30,
    backgroundColor: "#fff",
  },
  headtable: { height: 40, backgroundColor: "#f1f8ff" },
  texttable: { margin: 6 },

  scrollcontainer: {
    flexDirection: "row",
    backgroundColor: "white",
    margin: "1%",
    borderRadius: 20,
  },

  scrollcontainer2: {
    flexDirection: "row",
    marginLeft: "6%",
    alignContent: "center",
    alignItems: "center",
    justifyContent: "space-evenly",
  },

  graph: {
    flexDirection: "row",
    alignSelf: "center",
    alignContent: "center",
    alignItems: "center",
    justifyContent: "center",
  },

  ScrolIcon11: {
    width: "45%",
  },

  ScrolIcon12: {
    width: "5%",
  },

  ScrolIcon13: {
    width: "50%",
  },

  ScrolIcon1: {
    borderRadius: 20,
    display: "flex",
    backgroundColor: "#A3E5FF",
    flexDirection: "row",
    justifyContent: "space-evenly",
    margin: "1%",
    alignItems: "center",
    width: "40%",
    height: 100,
  },

  ScrolIcon2: {
    borderRadius: 20,
    borderColor: "#A3E5FF",
    borderWidth: 2,
    display: "flex",
    backgroundColor: "#A3E5FF",
    flexDirection: "row",
    justifyContent: "space-evenly",
    margin: "1%",
    alignItems: "center",
    width: "25%",
    height: 100,
  },

  ScrolIcon22: {
    borderRadius: 20,
    borderColor: "#A3E5FF",
    borderWidth: 2,
    display: "flex",
    backgroundColor: "#A3E5FF",
    flexDirection: "row",
    justifyContent: "space-evenly",
    margin: "1%",
    alignItems: "center",
    width: "13%",
    height: 50,
  },

  datebutton: {
    borderRadius: 20,
    borderColor: Config.color_1,
    borderWidth: 2,
    display: "flex",
    backgroundColor: Config.color_1,
    flexDirection: "row",
    justifyContent: "space-evenly",
    margin: "1%",
    marginTop: "6%",
    alignItems: "center",
    width: 210,
    height: 30,
  },

  ScrolIcon3: {
    borderRadius: 20,
    borderColor: "#A3E5FF",
    borderWidth: 2,
    display: "flex",
    backgroundColor: "#A3E5FF",
    flexDirection: "row",
    justifyContent: "space-evenly",
    margin: "1%",
    alignItems: "center",
    width: "13%",
    height: 100,
  },

  ScrolIcon4: {
    borderRadius: 20,
    borderWidth: 2,
    display: "flex",
    flexDirection: "row",
    justifyContent: "space-evenly",
    margin: "1%",
    alignItems: "center",
    width: "13%",
    height: 100,
  },

  ScrolIcon5: {
    display: "flex",
    flexDirection: "column",
    justifyContent: "space-evenly",
    margin: "1%",
    alignItems: "center",
    width: "5%",
    height: 100,
  },

  container2: {
    flex: 1,
    marginLeft: "6%",
    marginRight: "1%",
    borderRadius: 15,
  },

  scrollView: {
    backgroundColor: "#E6E6E6",
    marginHorizontal: 20,
    borderRadius: 15,
  },

  text: {
    fontSize: 42,
  },

  titleText: {
    fontSize: 25,
    fontWeight: "bold",
    marginLeft: "1%",
    color: Config.color_1,
  },

  menu: {
    position: "absolute",
    flex: 1,
    alignSelf: "flex-start",
    alignItems: "center",
    justifyContent: "flex-start",
    width: "5%",
    flexDirection: "column",
    backgroundColor: Config.color_1,
    height: "100%",
  },

  container: {
    height: 100,
    width: "100%",
    alignItems: "center",
  },

  Image: {
    height: 80,
    width: 90,
    alignItems: "center",
    marginTop: "1%",
  },

  input: {
    backgroundColor: Config.color_1,
    borderRadius: 20,
    width: "33%",
    height: "25%",
    left: 51,
    top: "50%",
    alignItems: "center",
    justifyContent: "flex-start",
  },

  gap: {
    height: 10,
    width: 5,
  },

  field: {
    justifyContent: "center",
    borderRadius: 20,
    width: "90%",
    height: "25%",
    padding: 20,
    backgroundColor: "white",
  },

  button: {
    justifyContent: "center",
    alignItems: "center",
    borderRadius: 20,
    width: "90%",
    height: "25%",
    padding: 20,
    backgroundColor: Config.color_2,
  },
});

export default Statistics;
