const { View, StyleSheet, Image, Text } = require("react-native");
import React, { useEffect, useState } from "react";
import Config from "../../config";
import { AnimatedCircularProgress } from "react-native-circular-progress";
import {
  Ionicons,
  Entypo,
  MaterialCommunityIcons,
  FontAwesome,
} from "@expo/vector-icons";
import axios from "axios";
import {
  ScrollView,
  TextInput,
  TouchableOpacity,
} from "react-native-gesture-handler";
import { Circle } from "react-native-svg";
import Loader from "react-loader-spinner";
import { TabRouter } from "@react-navigation/routers";
import Sidebar from "../dashboard/Sidebar";

const assignDrivers = ({ route, navigation }) => {
  const [driversSearch, onChangeDriversSearch] = useState("");
  const [allDrivers, setAllDrivers] = useState([]);
  const [drivers, setDrivers] = useState([]);
  const [showSideMenu, setShowSideMenu] = useState(false);
  const [editDriver, setEditDriver] = useState(null);
  const [isNewDriver, setIsNewDriver] = useState(false);
  const [loading, setLoading] = useState(true);
  const [mobilephone, onChangeMobilePhone] = useState("");
  const [email, onChangeEmail] = useState("");
  const [name, onChangeName] = useState("");
  const [maxOrders, onChangeMaxOrders] = useState("");
  const [password, onChangePassword] = useState("");
  const [password2, onChangePassword2] = useState("");
  const [showError, setShowError] = useState(false);
  const [errorHeader, setErrorHeader] = useState("");
  const [errorBody, setErrorBody] = useState("");

  const ShowErrorFunction = (header, body) => {
    setErrorHeader(header);
    setErrorBody(body);
    setShowError(true);
  };

  var branch = route.params.branch;

  const updateorder = (drivernumber, maxorders2, numoforders) => {
    // console.log("NUMBER OF ORDERS TO DRIVER");
    // console.log(numoforders);

    if (numoforders < maxorders2) {
      setLoading(true);
      setShowSideMenu(false);
      axios
        .patch(
          Config.host +
            Config.port +
            "/api/orders/assignorder/?order_id=" +
            route.params.order_id,
          {
            driver: drivernumber,
          }
        )

        .then((response) => {
          console.log(response);
          navigation.navigate("Restaurant");
        })
        .catch((e) => {
          console.log(e);
        });
    } else {
      // Do Something
      ShowErrorFunction("Failed", "Can't assign order to this driver, maximum delivered orders limit reached!")
      setLoading(false);
    }
  };

  useEffect(() => {
    setLoading(true);
    axios
      .get(
        Config.host +
          Config.port +
          "/api/profiles/getactivedrivers/?branch_number=" +
          branch
      )
      .then((response) => {
        console.log(response.data);
        setDrivers(response.data);
        setAllDrivers(response.data);
        setLoading(false);
      })
      .catch((e) => {
        console.log(e);
      });
  }, []);

  useEffect(() => {
    setLoading(true);
    var matchedDrivers = [];
    for (var i in allDrivers) {
      if (allDrivers[i].driver_name.includes(driversSearch)) {
        matchedDrivers.push(allDrivers[i]);
      } else if (allDrivers[i].mobilephone.includes(driversSearch)) {
        matchedDrivers.push(allDrivers[i]);
      }
    }
    if (matchedDrivers.length != 0) {
      setDrivers(matchedDrivers);
      setLoading(false);
    } else if (driversSearch == "") {
      setDrivers(allDrivers);
    } else {
      setDrivers([]);
      setLoading(false);
    }
  }, [driversSearch]);
  return (
    <View style={{ flex: 1, backgroundColor: Config.color_1 }}>
      <Sidebar navigation={navigation} />
      <View
        style={{
          height: "100%",
          width: "95%",
          justifyContent: "flex-end",
          alignSelf: "flex-end",
          flexDirection: "row",
        }}
      >
        <View
          style={{
            height: "100%",
            width: "100%",
            alignSelf: "flex-end",
          }}
        >
          <View
            style={{
              backgroundColor: "white",
              alignItems: "center",
              flex: 1,
            }}
          >
            <View style={styles.gap}></View>
            <View
              style={{
                flexDirection: "row",
                alignSelf: "flex-start",
                paddingLeft: "5%",
              }}
            >
              <Image
                style={{ width: 90, height: 80 }}
                source={require("../../assets/logo-01.png")}
              />
              <View style={styles.gap}></View>
              <Text
                style={{
                  paddingLeft: 10,
                  color: "black",
                  alignSelf: "center",
                  fontSize: 16,
                }}
              >
                Caboria Delivery Management System
              </Text>
            </View>
            <View
              style={{
                flexDirection: "row",
                alignSelf: "flex-end",
                paddingRight: "5%",
              }}
            >
              <View
                style={{
                  backgroundColor: Config.color_2,
                  width: 800,
                  height: 40,
                  borderRadius: 20,
                  alignContent: "center",
                }}
              >
                <View
                  style={{
                    width: "100%",
                    height: "100%",
                    flexDirection: "row",
                    justifyContent: "center",
                    alignItems: "center",
                    alignContent: "center",
                  }}
                >
                  <Ionicons size={24} name="search" onPress={() => {}} />
                  <View style={{ width: 20 }}></View>
                  <TextInput
                    style={{
                      borderColor: "black",
                      backgroundColor: "white",
                      borderRadius: 20,
                      width: "90%",
                      height: "80%",
                      paddingLeft: 20,
                    }}
                    autoCorrect={false}
                    placeholder={"Search Drivers"}
                    onChangeText={onChangeDriversSearch}
                  ></TextInput>
                </View>
              </View>
              <View style={{ width: 40 }} />
            </View>
            <View style={styles.gap}></View>
            <View style={styles.gap}></View>
            <View style={styles.gap}></View>
            <View style={styles.gap}></View>
            {loading ? (
              <View
                style={{
                  width: "90%",
                  height: "75%",
                  justifyContent: "center",
                  alignContent: "center",
                  alignSelf: "center",
                  alignItems: "center",
                }}
              >
                <Loader color={Config.color_1} />
              </View>
            ) : (
              <View
                style={{
                  backgroundColor: Config.color_1,
                  width: "90%",
                  height: "75%",
                  borderRadius: 20,
                }}
              >
                <View style={styles.gap}></View>
                <View style={styles.gap}></View>
                <ScrollView>
                  {drivers.map((driver) => (
                    <View key={driver.mobilephone}>
                      <View
                        style={{
                          width: "90%",
                          borderRadius: 20,
                          flexDirection: "row",
                          justifyContent: "space-evenly",
                          alignContent: "center",
                          alignSelf: "center",
                          backgroundColor: "#E6E6E6",
                        }}
                      >
                        <View
                          style={{
                            justifyContent: "center",
                            alignItems: "center",
                            padding: 20,
                            borderRadius: 20,
                          }}
                        >
                          {driver.image_64 == "" ? (
                            <Image
                              style={{ width: 110, height: 100 }}
                              source={require("../../assets/person.png")}
                            />
                          ) : (
                            <Image
                              style={{
                                width: 100,
                                height: 100,
                                overflow: "hidden",
                                borderRadius: 100,
                              }}
                              source={{
                                uri: driver.image_64,
                              }}
                            />
                          )}
                        </View>
                        <View style={{ width: 10 }}></View>
                        <View style={{ width: 10 }}></View>
                        <View
                          style={{
                            justifyContent: "center",
                            alignItems: "center",
                            alignContent: "center",
                            alignSelf: "center",
                          }}
                        >
                          <Ionicons
                            name="person"
                            size={20}
                            color={Config.color_2}
                          />
                          <View style={{ height: 5 }}></View>
                          <Text style={{ fontSize: 12, fontWeight: "bold" }}>
                            Driver Name
                          </Text>
                          <View style={{ height: 5 }}></View>
                          <View
                            style={{
                              alignItems: "center",
                              justifyContent: "center",
                              width: 200,
                            }}
                          >
                            <Text
                              style={{
                                fontSize: 12,

                                alignContent: "center",
                                alignItems: "center",
                                alignSelf: "center",
                                justifyContent: "center",
                              }}
                            >
                              {driver.driver_name}
                            </Text>
                          </View>
                        </View>
                        <View style={{ width: 10 }}></View>

                        <View
                          style={{
                            justifyContent: "center",
                            alignItems: "center",
                            alignContent: "center",
                            alignSelf: "center",
                          }}
                        >
                          <FontAwesome
                            size={25}
                            name="building"
                            color={Config.color_2}
                          />
                          <View style={{ height: 5 }}></View>
                          <Text style={{ fontSize: 12, fontWeight: "bold" }}>
                            Delivery Company
                          </Text>
                          <View style={{ height: 5 }}></View>
                          <View
                            style={{
                              alignItems: "center",
                              justifyContent: "center",
                              width: 100,
                            }}
                          >
                            <Text
                              style={{
                                fontSize: 12,

                                alignContent: "center",
                                alignItems: "center",
                                alignSelf: "center",
                                justifyContent: "center",
                              }}
                            >
                              {driver.company}
                            </Text>
                          </View>
                        </View>

                        <View style={{ width: 10 }}></View>

                        <View
                          style={{
                            justifyContent: "center",
                            alignItems: "center",
                          }}
                        >
                          <Ionicons
                            name="call"
                            size={20}
                            color={Config.color_2}
                          />
                          <View style={{ height: 5 }}></View>
                          <Text style={{ fontSize: 12, fontWeight: "bold" }}>
                            Mobile Number
                          </Text>
                          <View style={{ height: 5 }}></View>
                          <Text style={{ fontSize: 12 }}>
                            {driver.mobilephone}
                          </Text>
                        </View>
                        <View style={{ width: 10 }}></View>
                        <View
                          style={{
                            justifyContent: "center",
                            alignItems: "center",
                          }}
                        >
                          <Ionicons
                            name="list"
                            size={20}
                            color={Config.color_2}
                          />
                          <View style={{ height: 5 }}></View>
                          <Text style={{ fontSize: 12, fontWeight: "bold" }}>
                            Maximum Daily Orders
                          </Text>
                          <View style={{ height: 5 }}></View>
                          <Text>{driver.driver_max_orders}</Text>
                        </View>
                        <View style={{ width: 10 }}></View>
                        <View
                          style={{
                            justifyContent: "center",
                            alignItems: "center",
                          }}
                        >
                          <Text style={{ fontSize: 12, fontWeight: "bold" }}>
                            Today's Delivered Orders
                          </Text>
                          <View style={{ height: 8 }}></View>
                          <AnimatedCircularProgress
                            size={100}
                            width={15}
                            fill={parseInt(
                              (driver.driver_daily_orders /
                                driver.driver_max_orders) *
                                100
                            )}
                            rotation={0}
                            tintColor={Config.color_2}
                            dashedTint={{ width: 2, gap: 2 }}
                            tintColorSecondary={Config.color_1}
                            onAnimationComplete={() =>
                              console.log("onAnimationComplete")
                            }
                            backgroundColor={"white"}
                          >
                            {(fill) => (
                              <Text>{driver.driver_daily_orders}</Text>
                            )}
                          </AnimatedCircularProgress>
                        </View>
                        <View style={{ width: 10 }}></View>
                        <View
                          style={{
                            justifyContent: "center",
                            alignItems: "center",
                          }}
                        >
                          <View style={{ height: 5 }}></View>
                          <Text style={{ fontSize: 12, fontWeight: "bold" }}>
                            Assign
                          </Text>
                          <View style={{ height: 5 }}></View>
                          <Ionicons
                            name="checkmark-circle-sharp"
                            size={24}
                            onPress={() => {
                              //setShowSideMenu(true);
                              setEditDriver(driver);
                              updateorder(
                                driver.mobilephone,
                                driver.driver_max_orders,
                                driver.driver_daily_orders
                              );
                            }}
                          />
                        </View>
                      </View>

                      <View style={{ height: 10 }} />
                    </View>
                  ))}
                </ScrollView>
              </View>
            )}
            {showSideMenu ? (
              <View
                style={{
                  flex: 1,
                  position: "absolute",
                  right: "0%",
                  top: "0%",
                  height: "100%",
                  width: "30%",
                  backgroundColor: "#00000090",
                  justifyContent: "flex-start",
                  alignItems: "center",
                  zIndex: 999,
                }}
              >
                <View
                  style={{
                    padding: 5,
                    position: "absolute",
                    left: "0%",
                    top: "0%",
                  }}
                >
                  <Ionicons
                    size={25}
                    name="ios-close-circle"
                    onPress={() => {
                      setShowSideMenu(false);
                      setIsNewDriver(false);
                    }}
                  />
                </View>

                {isNewDriver ? (
                  <View
                    style={{
                      flex: 1,
                      justifyContent: "flex-start",
                      alignItems: "center",
                      width: "90%",
                    }}
                  >
                    <View style={{ height: 10 }}></View>
                    <View style={{ height: 10 }}></View>
                    <View
                      style={{
                        justifyContent: "center",
                        alignItems: "center",
                        width: "100%",
                      }}
                    >
                      <Ionicons
                        name="person"
                        size={20}
                        color={Config.color_2}
                      />
                      <View style={{ height: 5 }}></View>
                      <View style={{ height: 5 }}></View>
                      <TextInput
                        style={{
                          borderColor: "white",
                          backgroundColor: "white",
                          borderRadius: 20,
                          width: "80%",
                          height: 40,
                          paddingLeft: 15,
                        }}
                        autoCorrect={false}
                        placeholder={"Driver's Name"}
                        placeholderTextColor="black"
                        onChangeText={onChangeName}
                        backgroundColor="white"
                      ></TextInput>
                    </View>
                    <View style={{ height: 10 }}></View>
                    <View style={{ height: 10 }}></View>

                    <View
                      style={{
                        justifyContent: "center",
                        alignItems: "center",
                        width: "100%",
                      }}
                    >
                      <Ionicons name="call" size={20} color={Config.color_2} />
                      <View style={{ height: 5 }}></View>

                      <View style={{ height: 5 }}></View>
                      <TextInput
                        style={{
                          borderColor: "white",
                          backgroundColor: "white",
                          borderRadius: 20,
                          width: "80%",
                          height: 40,
                          paddingLeft: 15,
                        }}
                        autoCompleteType={"tel"}
                        autoCorrect={false}
                        placeholder={"Mobile Phone"}
                        placeholderTextColor="black"
                        backgroundColor="white"
                        onChangeText={onChangeMobilePhone}
                      ></TextInput>
                    </View>
                    <View style={{ height: 10 }}></View>
                    <View style={{ height: 10 }}></View>
                    <View
                      style={{
                        justifyContent: "center",
                        alignItems: "center",
                        width: "100%",
                      }}
                    >
                      <Ionicons name="mail" size={20} color={Config.color_2} />
                      <View style={{ height: 5 }}></View>

                      <View style={{ height: 5 }}></View>
                      <TextInput
                        style={{
                          borderColor: "white",
                          backgroundColor: "white",
                          borderRadius: 20,
                          width: "80%",
                          height: 40,
                          paddingLeft: 15,
                        }}
                        autoCorrect={false}
                        placeholder={"Email"}
                        placeholderTextColor="black"
                        backgroundColor="white"
                        onChangeText={onChangeEmail}
                      ></TextInput>
                    </View>
                    <View style={{ height: 10 }}></View>
                    <View style={{ height: 10 }}></View>
                    <View
                      style={{
                        justifyContent: "center",
                        alignItems: "center",
                        width: "100%",
                      }}
                    >
                      <Ionicons name="list" size={20} color={Config.color_2} />
                      <View style={{ height: 5 }}></View>

                      <View style={{ height: 5 }}></View>
                      <TextInput
                        style={{
                          borderColor: "white",
                          backgroundColor: "white",
                          borderRadius: 20,
                          width: "80%",
                          height: 40,
                          paddingLeft: 15,
                        }}
                        autoCorrect={false}
                        placeholder={"Maximum Daily Order"}
                        placeholderTextColor="black"
                        backgroundColor="white"
                        onChangeText={onChangeMaxOrders}
                      ></TextInput>
                    </View>
                    <View style={{ height: 10 }}></View>
                    <View style={{ height: 10 }}></View>
                    <View
                      style={{
                        justifyContent: "center",
                        alignItems: "center",
                        width: "100%",
                      }}
                    >
                      <Ionicons
                        name="lock-closed"
                        size={20}
                        color={Config.color_2}
                      />
                      <View style={{ height: 5 }}></View>

                      <View style={{ height: 5 }}></View>
                      <TextInput
                        style={{
                          borderColor: "white",
                          backgroundColor: "white",
                          borderRadius: 20,
                          width: "80%",
                          height: 40,
                          paddingLeft: 15,
                        }}
                        autoCompleteType={"tel"}
                        autoCorrect={false}
                        placeholder={"Password"}
                        textContentType={"password"}
                        secureTextEntry={true}
                        placeholderTextColor="black"
                        backgroundColor="white"
                        onChangeText={onChangePassword}
                      ></TextInput>
                    </View>
                    <View style={{ height: 10 }}></View>
                    <View style={{ height: 10 }}></View>
                    <View
                      style={{
                        justifyContent: "center",
                        alignItems: "center",
                        width: "100%",
                      }}
                    >
                      <Ionicons
                        name="lock-closed"
                        size={20}
                        color={Config.color_2}
                      />
                      <View style={{ height: 5 }}></View>

                      <View style={{ height: 5 }}></View>
                      <TextInput
                        style={{
                          borderColor: "white",
                          backgroundColor: "white",
                          borderRadius: 20,
                          width: "80%",
                          height: 40,
                          paddingLeft: 15,
                        }}
                        secureTextEntry={true}
                        textContentType={"password"}
                        autoCorrect={false}
                        placeholder={"Confirm Password"}
                        placeholderTextColor="black"
                        backgroundColor="white"
                        onChangeText={onChangePassword2}
                      ></TextInput>
                    </View>
                    <View style={{ height: 10 }}></View>
                    <View style={{ height: 10 }}></View>
                    <View style={{ height: 10 }}></View>
                    <View style={{ height: 10 }}></View>
                    <TouchableOpacity
                      style={{
                        backgroundColor: Config.color_2,

                        width: 200,
                        height: 40,
                        borderRadius: 20,
                        justifyContent: "center",
                        flexDirection: "row",
                        alignItems: "center",
                      }}
                      onPress={addDriver}
                    >
                      <Ionicons size={24} name="add" />
                      <View style={{ width: 20 }} />
                      <Text style={{ fontWeight: "bold" }}>Add Driver</Text>
                    </TouchableOpacity>
                  </View>
                ) : (
                  <View
                    style={{
                      flex: 1,
                      justifyContent: "flex-start",
                      alignItems: "center",
                      width: "90%",
                    }}
                  >
                    <View
                      style={{
                        justifyContent: "center",
                        alignItems: "center",
                        padding: 20,
                        borderRadius: 20,
                      }}
                    >
                      <Image
                        style={{
                          width: 100,
                          height: 100,
                          overflow: "hidden",
                          borderRadius: 100,
                        }}
                        source={{
                          uri: editDriver.profile_image,
                        }}
                      />
                    </View>
                    <View style={{ height: 10 }}></View>
                    <View style={{ height: 10 }}></View>
                    <View
                      style={{
                        justifyContent: "center",
                        alignItems: "center",
                        width: "100%",
                      }}
                    >
                      <Ionicons
                        name="person"
                        size={20}
                        color={Config.color_2}
                      />
                      <View style={{ height: 5 }}></View>
                      <View style={{ height: 5 }}></View>
                      <TextInput
                        style={{
                          borderColor: "white",
                          backgroundColor: "white",
                          borderRadius: 20,
                          width: "80%",
                          height: 40,
                          paddingLeft: 15,
                        }}
                        autoCorrect={false}
                        placeholder={"Driver's Name"}
                        placeholderTextColor="black"
                        onChangeText={onChangeName}
                        backgroundColor="white"
                      ></TextInput>
                    </View>
                    <View style={{ height: 10 }}></View>
                    <View style={{ height: 10 }}></View>

                    <View
                      style={{
                        justifyContent: "center",
                        alignItems: "center",
                        width: "100%",
                      }}
                    >
                      <Ionicons name="call" size={20} color={Config.color_2} />
                      <View style={{ height: 5 }}></View>

                      <View style={{ height: 5 }}></View>
                      <TextInput
                        style={{
                          borderColor: "white",
                          backgroundColor: "white",
                          borderRadius: 20,
                          width: "80%",
                          height: 40,
                          paddingLeft: 15,
                        }}
                        autoCompleteType={"tel"}
                        autoCorrect={false}
                        placeholder={"Mobile Phone"}
                        placeholderTextColor="black"
                        backgroundColor="white"
                        onChangeText={onChangeMobilePhone}
                      ></TextInput>
                    </View>
                    <View style={{ height: 10 }}></View>
                    <View style={{ height: 10 }}></View>
                    <View
                      style={{
                        justifyContent: "center",
                        alignItems: "center",
                        width: "100%",
                      }}
                    >
                      <Ionicons name="list" size={20} color={Config.color_2} />
                      <View style={{ height: 5 }}></View>

                      <View style={{ height: 5 }}></View>
                      <TextInput
                        style={{
                          borderColor: "white",
                          backgroundColor: "white",
                          borderRadius: 20,
                          width: "80%",
                          height: 40,
                          paddingLeft: 15,
                        }}
                        autoCompleteType={"tel"}
                        autoCorrect={false}
                        placeholder={"Maximum Daily Order"}
                        placeholderTextColor="black"
                        backgroundColor="white"
                        onChangeText={onChangeMaxOrders}
                      ></TextInput>
                    </View>
                    <View style={{ height: 10 }}></View>
                    <View style={{ height: 10 }}></View>
                    <View style={{ height: 10 }}></View>
                    <View style={{ height: 10 }}></View>
                    <TouchableOpacity
                      style={{
                        backgroundColor: Config.color_2,

                        width: 200,
                        height: 40,
                        borderRadius: 20,
                        justifyContent: "center",
                        flexDirection: "row",
                        alignItems: "center",
                      }}
                      onPress={updateDriver}
                    >
                      <Ionicons size={24} name="save" />
                      <View style={{ width: 20 }} />
                      <Text style={{ fontWeight: "bold" }}>Save</Text>
                    </TouchableOpacity>
                  </View>
                )}
              </View>
            ) : (
              <View />
            )}

            {showError ? (
              <View
                style={{
                  backgroundColor: Config.color_2,
                  position: "absolute",
                  alignSelf: "center",
                  marginTop: "20%",
                  width: 400,
                  height: 200,
                  borderRadius: 20,
                  // alignItems: "center",
                  // justifyContent: "center",
                  zIndex: 888,
                }}
              >
                <View
                  style={{
                    flexDirection: "row",
                    width: "100%",
                    justifyContent: "space-between",
                  }}
                >
                  <Image
                    style={{
                      width: 34,
                      height: 30,
                      marginTop: "5%",
                      marginLeft: "5%",
                    }}
                    source={require("../../assets/logo-01.png")}
                  />

                  <View
                    style={{
                      alignSelf: "flex-end",
                      marginTop: "5%",
                      marginRight: "5%",
                    }}
                  >
                    <Entypo
                      name="cross"
                      size={24}
                      color="black"
                      onPress={() => {
                        setShowError(false);
                      }}
                    />
                  </View>
                </View>

                <Text
                  style={{
                    alignSelf: "center",
                    fontWeight: "bold",
                    color: "red",
                    fontSize: 24,
                  }}
                >
                  {errorHeader}
                </Text>
                <View style={{ margin: "5%" }}>
                  <Text
                    style={{
                      alignSelf: "center",
                      fontWeight: "bold",
                      fontSize: 16,
                    }}
                  >
                    {errorBody}
                  </Text>
                </View>
                <View style={{ height: 5 }}></View>
              </View>
            ) : (
              <View></View>
            )}
          </View>
        </View>
      </View>
    </View>
  );
};
