import React, { useEffect, useState } from "react";
import {
    Image,
    SafeAreaView,
    ScrollView,
    StyleSheet,
    Text,
    TextInput,
    TouchableOpacity,
    View,
    Picker
} from "react-native";
import Config from "../../config";
import {
    Ionicons,
    FontAwesome,
    FontAwesome5,
    MaterialCommunityIcons,
    Octicons
} from "@expo/vector-icons";
import {
    withScriptjs,
    withGoogleMap,
    GoogleMap,
    Marker,
} from "react-google-maps";
import axios from "axios";
import MarkerWithLabel from "react-google-maps/lib/components/addons/MarkerWithLabel";
import { StatusBar } from "expo-status-bar";
import Loader from "react-loader-spinner";
import moment, { now } from "moment";
import Sidebar from "../dashboard/Sidebar";

import SortedFilteredTable from "./sortedFilteredTable"
import CalendarPicker from "react-native-calendar-picker";
import SortedFilteredDriverReport from "./sortedFilterdDriverReport";





const ReportDriver = ({ navigation }) => {
    const [selectedValueBranch, setSelectedValueBranch] = useState("-1");
    const [selectedValueCompany, setSelectedValueCompany] = useState("1");
    const [selectedValueDriver, setSelectedValueDriver] = useState("-1");
    const [selectedStartDate, onStartDateChange] = useState(null);
    const [selectedEndDate, onEndDateChange] = useState(null);
    const [branches, setBranches] = useState([[]]);
    const [companies, setCompanies] = useState([[]]);
    const [drivers, setDrivers] = useState([[]]);
    const [orders, setOrders] = useState([]);
    const [admin, setAdmin] = useState({
        account_type: "NA"
    });

    const [adminBranch, setAdminBranch] = useState("-1");
    const [loading, setLoading] = useState([]);

    useEffect(() => {
        setLoading(true)
        axios
            .get(Config.host + Config.port + "/api/orders/getallbranches/")
            .then((response) => {
                setBranches(response.data);
                axios
                    .get(
                        Config.host +
                        Config.port +
                        "/api/account/getaccountbytoken/?token=" +
                        Config.token
                    )
                    .then((adminnResponse) => {
                        setAdmin(adminnResponse.data);
                        console.log(admin)
                        setLoading(true)
                        if (adminnResponse.data.account_type == "Co") {


                            setOrders([])
                            console.log(Config.host +
                                Config.port +
                                "/api/profiles/getdriverbycompany/?company_number=" +
                                adminnResponse.data.mphone)
                            setSelectedValueCompany(adminnResponse.data.mphone)

                            axios
                                .get(
                                    Config.host +
                                    Config.port +
                                    "/api/profiles/getdriverbycompany/?company_number=" +
                                    adminnResponse.data.mphone
                                )
                                .then((response) => {
                                    console.log(response.data)
                                    setDrivers(response.data)
                                    setLoading(false)
                                })
                                .catch((e) => {
                                    console.log(e)
                                    setLoading(false)
                                })

                        } else if (adminnResponse.data.account_type == "A") {

                            axios
                                .get(
                                    Config.host +
                                    Config.port +
                                    "/api/account/getbranchbytoken/?token=" +
                                    Config.token
                                )
                                .then((response) => {
                                    console.log(response.data);
                                    setAdminBranch(response.data);
                                    setSelectedValueBranch(response.data);
                                    setLoading(false)

                                })
                                .catch((e) => {
                                    console.log(e);
                                    setLoading(false)

                                });
                        }
                        else {
                            setLoading(false)
                        }
                    });
            }
            )
            .catch((e) => {
                console.log(e)
                setLoading(false)
            })


        axios
            .get(Config.host + Config.port + "/api/companies/getallcompanies/")
            .then((response) => {
                console.log(response.data)

                setCompanies(response.data);
                setLoading(false);

            }
            )
            .catch((e) => {
                console.log(e)
                setLoading(false)
            })



    }, [])

    useEffect(() => {


        if (selectedValueBranch != "-1") {
            setLoading(true)
            setDrivers([])

            axios
                .get(
                    Config.host +
                    Config.port +
                    "/api/profiles/getdriverbybranch/?branch_number=" +
                    selectedValueBranch.toString()
                )
                .then((response) => {
                    console.log(response.data)
                    setDrivers(response.data)
                    setLoading(false)
                })
                .catch((e) => {
                    console.log(e)
                    setLoading(false)
                })
        }

    }, [selectedValueBranch])


    const getOrders = () => {
        
        if (selectedValueDriver != "-1" && selectedStartDate != null && selectedEndDate != null ) {
            setLoading(true)
            axios
                .get(
                    Config.host +
                    Config.port +
                    "/api/profiles/getdriverordersandattendance/?mphone=" +
                    selectedValueDriver + "&start_date=" + selectedStartDate.format("YYYY-MM-DD") + "&end_date=" + selectedEndDate.format("YYYY-MM-DD")
                )
                .then((response) => {
                    console.log(response.data)
                    setOrders(response.data)
                    setLoading(false)
                })
                .catch((e) => {
                    console.log(e)
                    setLoading(false)
                })
        }

    }
    




    useEffect(() => {


        if (admin.account_type == "Co") {
            setLoading(true)
            setDrivers([])
            console.log(Config.host +
                Config.port +
                "/api/profiles/getdriverbycompany/?company_number=" +
                admin.mphone)

            axios
                .get(
                    Config.host +
                    Config.port +
                    "/api/profiles/getdriverbycompany/?company_number=" +
                    admin.mphone
                )
                .then((response) => {
                    console.log(response.data)

                    setDrivers(response.data)
                    setLoading(false)
                })
                .catch((e) => {
                    console.log(e)
                    setLoading(false)
                })


        }
        else if (admin.account_type == "A" || admin.account_type == "SA") {
            setLoading(true)
            setDrivers([])
            console.log(Config.host +
                Config.port +
                "/api/profiles/getdriverbycompany/?company_number=" +
                selectedValueCompany)

            axios
                .get(
                    Config.host +
                    Config.port +
                    "/api/profiles/getdriverbycompany/?company_number=" +
                    selectedValueCompany
                )
                .then((response) => {

                    console.log(response.data)
                    setDrivers(response.data)
                    setLoading(false)
                })
                .catch((e) => {
                    console.log(e)
                    setLoading(false)
                })

            if (selectedValueCompany == -1) {
                var branchsend = adminBranch
                if(Config.accountType== "SA"){
                    branchsend=  selectedValueBranch
                }
                axios
                    .get(
                        Config.host +
                        Config.port +
                        "/api/profiles/getdriverbybranch/?branch_number=" +
                        branchsend.toString()
                    )
                    .then((response) => {
                        console.log(response.data)
                        setDrivers(response.data)
                        setLoading(false)
                    })
                    .catch((e) => {
                        console.log(e)
                        setLoading(false)
                    })
            }


        }



    }, [selectedValueCompany])




    return (
        <View style={{ flex: 1, backgroundColor: "#FFFF" }}>

            <View style={styles.gap}></View>
            <View
                style={{
                    flexDirection: "row",
                    alignSelf: "flex-start",
                    paddingLeft: "7%",
                }}
            >
                <Image
                    style={{ width: 90, height: 80 }}
                    source={require("../../assets/logo-01.png")}
                />
                <View style={styles.gap}></View>
                <Text
                    style={{
                        paddingLeft: 10,
                        color: Config.color_1,
                        fontWeight: "bold",
                        alignSelf: "center",
                        fontSize: 24,
                    }}
                >
                    Drivers Reports Page
                </Text>
            </View>
            <View style={{ width: 20, height: "5%" }} />

            <Sidebar navigation={navigation} />

            <View
                style={{
                    flexDirection: "row",
                    alignSelf: "flex-end",
                    paddingRight: "5%",
                }}
            >
                <View style={{ width: 40 }} />

                <View style={styles.gap}></View>
                <View style={styles.gap}></View>
            </View>
            <View style={styles.container2}>
                <View style={styles.gap}></View>
                <View style={styles.gap}></View>


                <View style={{ flexDirection: "row" }}>
                    <View
                        style={{
                            justifyContent: "center",
                            alignItems: "flex-start",
                            flexDirection: "column",
                            width: "50%",

                        }}
                    > {
                            admin.account_type == "SA" ? <View
                                style={{
                                    flexDirection: "row",
                                    alignItems: "center",
                                    width: "80%",
                                    marginHorizontal: "10%",

                                }}
                            >
                                <Text
                                    style={{
                                        color: Config.color_1,
                                        fontSize: 20,
                                        fontWeight: "bold",
                                        marginLeft: "8%",
                                        marginRight: "5.5%",
                                        alignContent: "center",
                                        alignItems: "center",
                                        alignSelf: "center",
                                    }}
                                >
                                    <Ionicons
                                        size={20}
                                        name="business-sharp"
                                        color="black"
                                    />
                                    {"  "}
                                    Branch
                                </Text>

                                <Picker
                                    style={{
                                        borderColor: Config.color_1,
                                        backgroundColor: Config.color_1,
                                        borderRadius: 20,
                                        width: "50%",
                                        height: 40,
                                        paddingLeft: 15,
                                    }}
                                    selectedValue={selectedValueBranch}
                                    onValueChange={(itemValue, itemIndex) =>
                                        setSelectedValueBranch(itemValue)
                                    }
                                >
                                    <Picker.Item label="Select branch" value={-1} />
                                    {branches.map((branches) => (
                                        <Picker.Item key={branches} label={branches[1]} value={branches[0]} />
                                    ))}
                                </Picker>
                            </View>

                                : <View></View>
                        }



                        <View style={{ height: "15%", width: "5%" }} />




                        {
                            admin.account_type == "A" || admin.account_type == "SA" ? <View
                                style={{
                                    flexDirection: "row",
                                    alignItems: "center",
                                    width: "80%",
                                    marginHorizontal: "10%",


                                }}
                            >
                                <Text
                                    style={{
                                        color: Config.color_1,
                                        fontSize: 20,
                                        fontWeight: "bold",
                                        marginLeft: "8%",
                                        marginRight: "3.5%",
                                        alignContent: "center",
                                        alignItems: "center",
                                        alignSelf: "center",
                                    }}
                                >

                                    <Octicons name="organization" size={20} color="black" />
                                    {"  "}
                                    Company
                                </Text>

                                <Picker
                                    style={{
                                        borderColor: Config.color_1,
                                        backgroundColor: Config.color_1,
                                        borderRadius: 20,
                                        width: "50%",
                                        height: 40,
                                        paddingLeft: 15,
                                    }}
                                    selectedValue={selectedValueCompany}
                                    onValueChange={(itemValue, itemIndex) =>
                                        setSelectedValueCompany(itemValue)
                                    }
                                >
                                    <Picker.Item label="Select company" value={-1} />
                                    {companies.map((company) => (
                                        <Picker.Item key={company.company_number} label={company.company_name} value={company.company_number} />
                                    ))}
                                </Picker>
                            </View> : <View></View>
                        }


                        <View style={{ height: "15%", width: "5%" }} />




                        <View
                            style={{
                                flexDirection: "row",
                                alignItems: "center",
                                width: "80%",
                                marginHorizontal: "10%",

                            }}
                        >
                            <Text
                                style={{
                                    color: Config.color_1,
                                    fontSize: 20,
                                    fontWeight: "bold",
                                    marginLeft: "8%",
                                    marginRight: "6%",
                                    alignContent: "center",
                                    alignItems: "center",
                                    alignSelf: "center",
                                }}
                            >
                                <FontAwesome5 name="id-card-alt" size={20} color="black" />
                                {"  "}
                                Driver
                            </Text>

                            <Picker
                                style={{
                                    borderColor: Config.color_1,
                                    backgroundColor: Config.color_1,
                                    borderRadius: 20,
                                    width: "50%",
                                    height: 40,
                                    paddingLeft: 15,
                                }}
                                selectedValue={selectedValueDriver}
                                onValueChange={(itemValue, itemIndex) =>
                                    setSelectedValueDriver(itemValue)
                                }
                            >
                                <Picker.Item label="Select driver" value={-1} />
                                {drivers.map((driver) => (
                                    <Picker.Item key={driver.mobilephone} label={driver.driver_name} value={driver.mobilephone} />
                                ))}
                            </Picker>
                        </View>
                        <View style={{ width: "10%", height: "10%" }} />




                    </View>

                    <View style={{ flexDirection: "column", alignItems: "center" }}>
                        <Text style={{ fontWeight: "bold" }}>Start date</Text>
                        <CalendarPicker
                            selectedDayColor={Config.color_1}
                            onDateChange={onStartDateChange}
                            width="600"
                            height="300"
                        />

                    </View>

                    <View style={{ width: "2%", height: "10%" }} />


                    <View style={{ flexDirection: "column", alignItems: "center" }}>
                        <Text style={{ fontWeight: "bold" }}>End date</Text>
                        <CalendarPicker
                            selectedDayColor={Config.color_1}
                            onDateChange={onEndDateChange}
                            width="600"
                            height="300"
                        />


                    </View>

                    <View style={{ width: "2%", height: "10%" }} />

                    <Ionicons name="search-circle" size={44} color="black" onPress={getOrders} />








                </View>





                <View style={styles.gap}></View>
                <View style={styles.gap}></View>

                {
                    loading ? (
                        <View
                            style={{
                                width: "90%",
                                height: "75%",
                                justifyContent: "center",
                                alignContent: "center",
                                alignSelf: "center",
                                alignItems: "center",
                            }}
                        >
                            <Loader color={Config.color_1} />
                        </View>
                    ) : (

                        orders.length != 0 ? <ScrollView>

                            <SortedFilteredDriverReport orders={orders} />
                        </ScrollView> : <View
                            style={{
                                width: "90%",
                                height: "75%",
                                justifyContent: "center",
                                alignContent: "center",
                                alignSelf: "center",
                                alignItems: "center",
                            }}
                        >

                            {selectedValueBranch == '-1' && selectedValueCompany == '1' ?
                                <Text>Please select a branch or a company</Text>
                                :
                                selectedValueDriver == "-1" ?
                                    <Text>Please select a driver</Text> : selectedStartDate == null ? <Text>Please select start date</Text> : selectedEndDate == null ? <Text>Please select end date</Text> : <Text>Please click the search icon</Text>

                            }
                        </View>

                    )
                }





            </View>
        </View>
    )

}



var styles = StyleSheet.create({
    containertable: {
        flex: 1,
        padding: 16,
        paddingTop: 30,
        backgroundColor: "#fff",
    },
    headtable: { height: 40, backgroundColor: "#f1f8ff" },
    texttable: { margin: 6 },

    scrollcontainer: {
        flexDirection: "row",
        backgroundColor: "white",
        margin: "1%",
        borderRadius: 20,
    },

    scrollcontainer2: {
        flexDirection: "row",
        marginLeft: "6%",
        alignContent: "center",
        alignItems: "center",
        justifyContent: "space-evenly",
    },

    graph: {
        flexDirection: "row",
        alignSelf: "center",
        alignContent: "center",
        alignItems: "center",
        justifyContent: "center",
    },

    ScrolIcon11: {
        width: "45%",
    },

    ScrolIcon12: {
        width: "5%",
    },

    ScrolIcon13: {
        width: "50%",
    },

    ScrolIcon1: {
        borderRadius: 20,
        display: "flex",
        backgroundColor: "#A3E5FF",
        flexDirection: "row",
        justifyContent: "space-evenly",
        margin: "1%",
        alignItems: "center",
        width: "40%",
        height: 100,
    },

    ScrolIcon2: {
        borderRadius: 20,
        borderColor: "#A3E5FF",
        borderWidth: 2,
        display: "flex",
        backgroundColor: "#A3E5FF",
        flexDirection: "row",
        justifyContent: "space-evenly",
        margin: "1%",
        alignItems: "center",
        width: "25%",
        height: 100,
    },

    ScrolIcon22: {
        borderRadius: 20,
        borderColor: "#A3E5FF",
        borderWidth: 2,
        display: "flex",
        backgroundColor: "#A3E5FF",
        flexDirection: "row",
        justifyContent: "space-evenly",
        margin: "1%",
        alignItems: "center",
        width: "13%",
        height: 50,
    },

    datebutton: {
        borderRadius: 20,
        borderColor: Config.color_1,
        borderWidth: 2,
        display: "flex",
        backgroundColor: Config.color_1,
        flexDirection: "row",
        justifyContent: "space-evenly",
        margin: "1%",
        marginTop: "6%",
        alignItems: "center",
        width: 210,
        height: 30,
    },

    ScrolIcon3: {
        borderRadius: 20,
        borderColor: "#A3E5FF",
        borderWidth: 2,
        display: "flex",
        backgroundColor: "#A3E5FF",
        flexDirection: "row",
        justifyContent: "space-evenly",
        margin: "1%",
        alignItems: "center",
        width: "13%",
        height: 100,
    },

    ScrolIcon4: {
        borderRadius: 20,
        borderWidth: 2,
        display: "flex",
        flexDirection: "row",
        justifyContent: "space-evenly",
        margin: "1%",
        alignItems: "center",
        width: "13%",
        height: 100,
    },

    ScrolIcon5: {
        display: "flex",
        flexDirection: "column",
        justifyContent: "space-evenly",
        margin: "1%",
        alignItems: "center",
        width: "5%",
        height: 100,
    },

    container2: {
        flex: 1,
        marginLeft: "6%",
        marginRight: "1%",
        borderRadius: 15,
    },

    scrollView: {
        backgroundColor: "#E6E6E6",
        marginHorizontal: 20,
        borderRadius: 15,
    },

    text: {
        fontSize: 42,
    },

    titleText: {
        fontSize: 25,
        fontWeight: "bold",
        marginLeft: "1%",
        color: Config.color_1,
    },

    menu: {
        position: "absolute",
        flex: 1,
        alignSelf: "flex-start",
        alignItems: "center",
        justifyContent: "flex-start",
        width: "5%",
        flexDirection: "column",
        backgroundColor: Config.color_1,
        height: "100%",
    },

    container: {
        height: 100,
        width: "100%",
        alignItems: "center",
    },

    Image: {
        height: 80,
        width: 90,
        alignItems: "center",
        marginTop: "1%",
    },

    input: {
        backgroundColor: Config.color_1,
        borderRadius: 20,
        width: "33%",
        height: "25%",
        left: 51,
        top: "50%",
        alignItems: "center",
        justifyContent: "flex-start",
    },

    gap: {
        height: 10,
    },

    field: {
        justifyContent: "center",
        borderRadius: 20,
        width: "90%",
        height: "25%",
        padding: 20,
        backgroundColor: "white",
    },

    button: {
        justifyContent: "center",
        alignItems: "center",
        borderRadius: 20,
        width: "90%",
        height: "25%",
        padding: 20,
        backgroundColor: Config.color_2,
    },
});



export default ReportDriver;
