import React, { useState } from "react";
import {
  SafeAreaView,
  StyleSheet,
  Text,
  Image,
  View,
  ImageBackground,
  TextInput,
  Touchable,
  TouchableOpacity,
  ActivityIndicator,
} from "react-native";
import Config from "../../config";
import { Ionicons, Entypo } from "@expo/vector-icons";
import axios from "axios";
import Loader from "react-loader-spinner";

const Login = ({ navigation }) => {
  const [mphone, onChangeMphone] = useState(" ");
  const [password, onChangePassword] = useState(" ");
  const [loading, setLoading] = React.useState(false);
  const [showError, setShowError] = useState(false);
  const [errorHeader, setErrorHeader] = useState("");
  const [errorBody, setErrorBody] = useState("");

  const ShowErrorFunction = (header, body) => {
    setErrorHeader(header);
    setErrorBody(body);
    setShowError(true);
  };

  const login = async () => {
    var details = {
      username: mphone,
      password: password,
    };
    console.log(details);
    setLoading(true);
    axios
      .post(Config.host + Config.port + "/api/account/login", details, {
        headers: {
          "Content-Type": "application/json",
          Accept: "application/json",
        },
      })
      .then(async (response) => {
        setLoading(false);
        console.log(response.status);
        if (response.status == 200) {
          Config.token = response.data.token;
          Config.username = mphone;
          axios
            .get(
              Config.host +
              Config.port +
              "/api/account/getaccountbytoken/?token=" +
              Config.token
            )
            .then((response) => {
              if (response.data.is_company) {
                Config.accountType = "Co";
                navigation.navigate("CompanyMenu", {
                  token: Config.token,
                  company: response.data,
                });
              } else if (response.data.account_type == "A") {
                Config.accountType = "A";
                navigation.navigate("Menu", {
                  token: Config.token,
                });
              }
              // TODO: ACCOUNT TYPE CHECK ==> Set the account type in config and navigate to super admin menu
              else if (response.data.account_type == "SA") {
                Config.accountType = "SA";

                navigation.navigate("MenuSuperAdmin", {
                  token: Config.token,
                });
              } else {
                navigation.navigate("Login");

              }
              setShowError(false);
            });
        } else {
        }
        console.log(Config.accountType)
      })
      .catch(function (error) {
        ShowErrorFunction(
          "",
          "The mobile number or password that you've entered is incorrect, please try again! "
        );
        setLoading(false);
      });
  };
  return loading ? (
    <View
      style={{
        flex: 1,
        justifyContent: "center",
        alignContent: "center",
        alignSelf: "center",
      }}
    >
      <Loader color={Config.color_1} />
    </View>
  ) : (
    <ImageBackground
      style={styles.image}
      source={require("../../assets/unsplash.png")}
    >
      <View
        style={{
          top: "2%",
          left: "2%",
          alignSelf: "flex-start",
          alignItems: "center",
          justifyContent: "center",
          flexDirection: "row",
        }}
      >
        <Image
          style={{ width: 110, height: 100 }}
          source={require("../../assets/logo-01.png")}
        />
        <View style={styles.gap}></View>
        <Text
          style={{
            paddingLeft: 10,
            color: "white",
            fontWeight: "bold",
            fontSize: 18,
          }}
        >
          Caboria Delivery Management System
        </Text>
      </View>
      <View style={styles.input}>
        <View style={styles.gap}></View>
        <View style={styles.field}>
          <Ionicons size={15} name="call" />
          <View style={{ height: 2 }}></View>
          <TextInput
            style={{ borderColor: "white" }}
            autoCompleteType={"tel"}
            autoCorrect={false}
            placeholder={"Mobile Phone"}
            onChangeText={onChangeMphone}
          ></TextInput>
        </View>

        <View style={styles.gap}></View>
        <View style={styles.field}>
          <Ionicons size={15} name="lock-closed" />
          <View style={{ height: 2 }}></View>
          <TextInput
            autoCompleteType={"password"}
            secureTextEntry={true}
            autoCorrect={false}
            placeholder={"Password"}
            onChangeText={onChangePassword}
          ></TextInput>
        </View>

        <View style={styles.gap}></View>
        <TouchableOpacity style={styles.button} onPress={login}>
          <Text style={{ fontWeight: "bold" }}>Login</Text>
        </TouchableOpacity>
      </View>
      {showError ? (
        <View
          style={{
            backgroundColor: Config.color_2,
            position: "absolute",
            alignSelf: "flex-start",
            marginTop: "20%",
            left: 51,
            width: 400,
            height: 150,
            borderRadius: 20,
            // alignItems: "center",
            // justifyContent: "center",
            zIndex: 888,
          }}
        >
          <View
            style={{
              flexDirection: "row",
              width: "100%",
              justifyContent: "space-between",
            }}
          >
            <Image
              style={{
                width: 34,
                height: 30,
                marginTop: "5%",
                marginLeft: "5%",
              }}
              source={require("../../assets/logo-01.png")}
            />

            <View
              style={{
                alignSelf: "flex-end",
                marginTop: "5%",
                marginRight: "5%",
              }}
            >
              <Entypo
                name="cross"
                size={24}
                color="black"
                onPress={() => {
                  setShowError(false);
                }}
              />
            </View>
          </View>

          <Text
            style={{
              alignSelf: "center",
              fontWeight: "bold",
              color: "red",
              fontSize: 24,
            }}
          >
            {errorHeader}
          </Text>
          <View style={{ margin: "5%" }}>
            <Text
              style={{ alignSelf: "center", fontWeight: "bold", fontSize: 16 }}
            >
              {errorBody}
            </Text>
          </View>
          <View style={{ height: 5 }}></View>
        </View>
      ) : (
        <View></View>
      )}
    </ImageBackground>
  );
};

var styles = StyleSheet.create({
  container: {
    alignItems: "center",
    justifyContent: "center",
  },
  image: {
    flex: 1,
  },
  input: {
    backgroundColor: Config.color_1,
    borderRadius: 20,
    width: "33%",
    height: "25%",
    left: 51,
    top: "50%",
    alignItems: "center",
    justifyContent: "flex-start",
  },
  gap: {
    height: 10,
    width: 5,
  },
  field: {
    justifyContent: "center",
    borderRadius: 20,
    width: "90%",
    height: "20%",
    padding: 18,
    backgroundColor: "white",
  },
  button: {
    justifyContent: "center",
    alignItems: "center",
    borderRadius: 20,
    width: "90%",
    height: "20%",
    padding: 18,
    backgroundColor: Config.color_2,
  },
});
export default Login;
